import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {  Observable, throwError } from 'rxjs';

import { environment } from 'environments/environment';

import { catchError } from 'rxjs/operators';
import { FormBuilder, Validators } from '@angular/forms';
import { Asset } from 'app/models/asset';

@Injectable({
  providedIn: 'root'
})
export class AssetService {

  apiURLAssets = environment.apiUrl + 'assets';
  youTubeApiKey = 'AIzaSyCUGTLCPqwF0kLzy81zT6WdNnwQL8ArfMk';

  constructor(private http: HttpClient, private formBuilder:FormBuilder) { 
     // Set the defaults

  }

//Initiate Form

 _initForm(assetId:any,lecture:any,section:any,fileType:any){
  return this.formBuilder.group({
    id:assetId,
    lecture:lecture.id,
    description: ['', Validators.required],
    section:section,
    fileType:fileType,

    uploadedFile:['', Validators.required],
    uploadedFileName:'',
    uploadedFileSize:'',
    uploadedFileType:'',
    encodedFileName:'',
    pageCount:'',

    duration:'',
    hours:'',
    minutes:'',
    seconds:'',
    
    isPublished:true,
    isExternal:false,

    

  })
}   




  /**
   * Get API Data
   */
   getAsset(assetId: string): Observable<Asset> {
    return this.http.get<Asset>(`${this.apiURLAssets}/${assetId}`);
  }

/**
 *  Update Asset Video
 * @param assetsData 
 * @param assetId 
 * @returns 
 */


getYoutubeTitle(youtubeId): Observable<any> {
  let ytEndpoint = "https://www.googleapis.com/youtube/v3/videos?part=snippet&id=" + youtubeId + "&key=" + this.youTubeApiKey;
  return this.http.get<any>(ytEndpoint);
}

getYoutubeDuration(youtubeId): Observable<any> {
  let ytEndpoint = "https://www.googleapis.com/youtube/v3/videos?part=contentDetails&id=" + youtubeId + "&key=" + this.youTubeApiKey;
  return this.http.get<any>(ytEndpoint);
}


updateAsset(assetsData: FormData,assetId:string): Observable<any> {
  if (assetId) {
    return this.http.put(`${this.apiURLAssets}/${assetId}`, assetsData, {
      reportProgress: true,
      observe: 'events',
    })
    .pipe(
      catchError(this.handleError)); 
  }


  return this.http.post(`${this.apiURLAssets}`, assetsData, {
      reportProgress: true,
      observe: 'events',
    })
    .pipe(
      catchError(this.handleError));
}
// 



/**
 *  ---------------------- Create New Video
 * @param assetsData 
 * @returns 
 */
createVideoAsset(assetsData: FormData): Observable<any> {
  return this.http
    .post(`${this.apiURLAssets}`, assetsData, {
      reportProgress: true,
      observe: 'events',
    })
    .pipe(
      catchError(this.handleError));
}

private handleError(error: any) { 
  let errMsg = (error.message) ? error.message : error.status ? `${error.status} - ${error.statusText}` : 'Server error';
  return throwError(error);
}

/**
 *  ----------------------- Delete Video
 * @param assetId 
 * @returns 
 */

  deleteAsset(assetId: string): Observable<any> {
    return this.http.delete<any>(`${this.apiURLAssets}/${assetId}`);
  }

}
