import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AuthGuard } from 'app/auth/helpers/auth.guards';
import { CoreCommonModule } from '@core/common.module';
import { ContentHeaderModule } from 'app/layout/components/content-header/content-header.module';
import { Ng2FlatpickrModule } from 'ng2-flatpickr';
import { AccountSettingsComponent } from './account-settings/account-settings.component';
import { NgSelectModule } from '@ng-select/ng-select';
import { InstructorDashboardService } from './dashboard/instructor-dashboard.service';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { InstructorCoursesDatatableComponent } from './dashboard/courses-datatable/instructor-courses-datatable.component';
import { InstructorMenuComponent } from './dashboard/menu/instructor-menu.component';
import { InstructorDashboardHeaderComponent } from './dashboard/header/instructor-dashboard-header.component';
import { InstructorCoursesListComponent } from './dashboard/courses-list/instructor-courses-list.component';
import { InstructorCourseFormComponent } from './dashboard/course-form/instructor-course-form.component';
import { InstructorCourseFormDetailsComponent } from './dashboard/course-form/course-details/instructor-course-form-details.component';
import { InstructorCourseTargetStudentComponent } from './dashboard/course-form/course-target-student/instructor-course-target-student.component';
import { InstructorCourseContentComponent } from './dashboard/course-form/course-content/instructor-course-content.component';
import { FileUploadModule } from 'ng2-file-upload';
import { DragulaModule } from 'ng2-dragula';
import { InstructorcourseLecturesComponent } from './dashboard/course-form/course-content/lectures-list/instructor-course-lectures.component';
import { InstructorcourseAssetsComponent } from './dashboard/course-form/course-content/assets-list/instructor-course-assets.component';
import { AssetFilterPipe } from 'app/models/asset-filter.pipe';
import { AssetVideoFormComponent } from './dashboard/course-form/course-content/assets-list/video-component/asset-video-form.component';
import { AssetAudioFormComponent } from './dashboard/course-form/course-content/assets-list/audio-component/asset-audio-form.component';
import { AssetYoutubeFormComponent } from './dashboard/course-form/course-content/assets-list/youtube-component/asset-youtube-form.component';
import { AssetDocsFormComponent } from './dashboard/course-form/course-content/assets-list/docs-component/asset-docs-form.component';
import { CoreTouchspinModule } from '@core/components/core-touchspin/core-touchspin.module';
import { AssetExternalFormComponent } from './dashboard/course-form/course-content/assets-list/external-component/asset-external-form.component';
import { AssetDownloadableFormComponent } from './dashboard/course-form/course-content/assets-list/downloadable-component/asset-downloadable-form.component';
import { InstructorcourseTaskComponent } from './dashboard/course-form/course-task/instructor-course-task.component';
import { QuillModule } from 'ngx-quill';
import { LectureAssignmentFormComponent } from './dashboard/course-form/course-task/assignment-form/lecture-assignment-form.component';
import { AccountSettingsService } from 'app/services/account-settings.service';

const routes: Routes = [
  {
    path: 'account-settings',
    component: AccountSettingsComponent,
    canActivate: [AuthGuard],
    resolve: {
      accountSetting: AccountSettingsService
    },
    data: { animation: 'account-settings' }
  },
  
  {
    path: 'dashboard',
    component: InstructorCoursesListComponent,
    canActivate: [AuthGuard],
    resolve: {
      profile: InstructorDashboardService
    }
  },

  {
    path: 'new-course',
    component: InstructorCourseFormComponent,
    canActivate: [AuthGuard],
    resolve: {
      profile: InstructorDashboardService
    }
  },
  {
    path: 'create-course/:id',
    component: InstructorCourseFormComponent,
    canActivate: [AuthGuard],
    resolve: {
      profile: InstructorDashboardService
    }
  },
  {
    path: 'update-course/:id',
    component: InstructorCourseFormComponent,
    canActivate: [AuthGuard],
    resolve: {
      profile: InstructorDashboardService
    }
  }
];

@NgModule({
  declarations: [
    AssetFilterPipe,
    AccountSettingsComponent,
    InstructorCoursesListComponent,
    InstructorDashboardHeaderComponent,
    InstructorCoursesDatatableComponent,
    InstructorMenuComponent,
    InstructorCourseFormComponent,
    InstructorCourseFormDetailsComponent,
    InstructorCourseTargetStudentComponent,
    InstructorCourseContentComponent,
    InstructorcourseLecturesComponent,
    InstructorcourseAssetsComponent,
    AssetVideoFormComponent,
    AssetAudioFormComponent,
    AssetYoutubeFormComponent,
    AssetDocsFormComponent,
    AssetExternalFormComponent,
    AssetDownloadableFormComponent,
    InstructorcourseTaskComponent,
    LectureAssignmentFormComponent
],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    DragulaModule.forRoot(),
    NgbModule,
    CoreCommonModule,
    ContentHeaderModule,
    Ng2FlatpickrModule,
    NgSelectModule,
    NgxDatatableModule,
    FileUploadModule,
    CoreTouchspinModule,
    QuillModule.forRoot(),
  ],

  providers: [AccountSettingsService,InstructorDashboardService]
})
export class InstructorModule {}
