import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Chapter } from 'app/models/chapter';
import { Lecture } from 'app/models/lecture';
import { ChapterService } from 'app/services/chapter.service';

import Stepper from 'bs-stepper';
import { DragulaService } from 'ng2-dragula';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import Swal from 'sweetalert2';

@Component({
  selector: 'instructor-course-content',
  templateUrl: './instructor-course-content.component.html'
})

export class InstructorCourseContentComponent implements OnInit, OnDestroy {

  subscription = new Subscription();
  // public
  public error: string;
  current: any;
  editChapterMode: boolean = false;

  courseContentForm: FormGroup;

  @Input() appLanguage: string;
  @Input() course: any;
  @Output() courseUpdated = new EventEmitter<boolean>();
  // private
  private _unsubscribeAll: Subject<any>;
  public get unsubscribeAll(): Subject<any> {
    return this._unsubscribeAll;
  }
  public set unsubscribeAll(value: Subject<any>) {
    this._unsubscribeAll = value;
  }
  // private
  private courseWizardStepper: Stepper;

  constructor(
    private dragulaService: DragulaService,
    private formBuilder: FormBuilder,
    private chapterService: ChapterService,
    private toastr: ToastrService
  ) {
    this._unsubscribeAll = new Subject();

  }

  /**
 * On init --------------------------------------------------------
 */

  ngOnInit(): void {

    this.courseUpdated.emit(false);
    this.dragulaService.createGroup('chapters-list', {
      moves: function (el, container, handle) {
        return (handle.classList.contains('handle') && !handle.classList.contains('edit-mode'));
      }
    });

    this.dragulaService.out('chapters-list').subscribe((value) => {
      this.createCourseContent();
    });

    this.courseContentForm = this.formBuilder.group({
      chapters: this.formBuilder.array([]),
    })
    this._getChapters();
    this.courseWizardStepper = new Stepper(document.querySelector('#stepper1'), {});
  }

/**
 * Get updates from lecture child then update chapt
 * @param isUpdated 
 */


  updateCurrentChapters(isUpdated:boolean){
    if (isUpdated) {
      this.courseUpdated.emit(true);
    }
  }

  //------------------------ Chapters ------------------------

  /**
   * Get Chapters from DB
   *
   */
  private _getChapters() {
    this.chapterService.getChapters(this.course._id).subscribe(chapter => {
     this.editChapterMode = false;
   
      this.setChapterDefaultData(chapter);
    })
  }

  /**
  * Defaul requirement data
  */

  setChapterDefaultData(courseChapters) {
    this.deleteAll();
    for (let index = 0; index < courseChapters.length; index++) {
      this.addChapter(courseChapters[index]);
    }
  }

 /**
  * Delete all chapters to reset
  */

  deleteAll() {
    while (this.courseContentForm.get('chapters')['controls'].length !== 0) {
      (<FormArray>this.courseContentForm.controls['chapters']).removeAt(0);
    }
  }
  /**
  * Add new chapter item from db to fromArray
  */

  addChapter(chapter: Chapter) {
    let chapters = this.courseContentForm.get('chapters') as FormArray;
    chapters.push(this.createChaptersArray(chapter));
  }

  /**
  * Create new chapter item from db to fromArray
  */

  createChaptersArray(chapter: Chapter) {
  
    return this.formBuilder.group({
      id: chapter._id,
      sortNumber: chapter.sortNumber,
      title: [chapter.title, [Validators.required]],
      course: chapter.course,
      shown:false,
      lectures: (<any>chapter.lectures).length > 0 ? new FormArray([this.createLecturesArray(chapter.lectures)]) : this.formBuilder.array([])
    });
  }
  /**
  * Add Lecture item from db to fromArray
  */

  createLecturesArray(lecture: Lecture) {
    {
      return this.formBuilder.group({
        title: lecture.title,
        chapter: lecture.chapter,
        sortNumber: lecture.sortNumber
      })

    }
  }

  /**
  * Create new empty chapter
  *
  */
   createEmptyChaptersArray() {
    this.editChapterMode = true;
    let chapters = this.courseContentForm.get('chapters') as FormArray;
    chapters.push(
      this.formBuilder.group({
        id: null,
        title: ['', [Validators.required]],
        course: this.course._id,
        lectures: [],
        shown:false,
      })
    );
  }

  /**
  * Delete chapter and all its lectures
  *
  * @param index
  */
  deleteChapterItem(index: any) {
   this.checkDragValidation();
    const items = this.courseContentForm.get('chapters')['controls'];
    const chapter = this.courseContentForm.value.chapters;
    for (let i = 0; i < items.length; i++) {
    
      if (items.indexOf(items[i]) === index) {
        if (chapter[index].id) {
          this.chapterService.getLectures(chapter[index].id).subscribe(lectures => {
            if (lectures.length>0) {
              this.setErrorMessage('All lectures and content must be deleted before the chapter is deleted', 8000);
            }else{
              this.deleteChapter(chapter[index].id, i);
               }
           })
        }
        else //New item which is not stored in DB
        {
          (<FormArray>this.courseContentForm.controls['chapters']).removeAt(i);
          this.checkDragValidation();
        }
      }
    }
  }


  /**
  * Warning message to delete chapter
  *
  */

  deleteChapter(chapterId: string, index) {

    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-danger ml-1'
      }
    }).then((result) => {
      if (result.value) {
        this._deleteChapter(chapterId);
        (<FormArray>this.courseContentForm.controls['chapters']).removeAt(index);
        this.updateChapterSorting();
        Swal.fire({
          title: 'Deleted!',
          text: 'Chapter has been deleted.',
          icon: 'success',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        });
      }
      else {
        Swal.fire({
          title: 'Cancelled',
          text: 'Chapter has not been deleted',
          icon: 'error',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
       }
      }
    );
  }

  /**
  * Delete chapter from DB
  *
  */

  private _deleteChapter(chapterId) {
    this.chapterService.deleteChapter(chapterId).subscribe(
      () => { 
        this.courseUpdated.emit(true);
        this._getChapters();
      },
      () => {
        this.toastr.error('Category is not deleted!', 'Error!', {
          toastClass: 'toast ngx-toastr',
          closeButton: true
        });
        return false;
      }
    );
  }

  /**
  * Update chapters sorting after delete a chapter
  *
  */

  updateChapterSorting() {
    const chapters = this.courseContentForm.value.chapters;
    for (let index = 0; index < chapters.length; index++) {
      let chapter = this.courseContentForm.get('chapters')['controls'][index] as FormArray;
      chapter.value.sortNumber = index + 1;
      this.updateChapter(chapter.value);
    }
  }



  /**
  * Save new chapter to DB
  *
  */

  updateOrCreateChapter(chapter: any) {
    //const chapter = this.courseContentForm.value.chapters[index];
    const sortNumber = this.courseContentForm.value.chapters.length;
    if (chapter.id) {
      this.updateChapter(chapter);
    }
    else {
      chapter.sortNumber = sortNumber;
      this.createChapter(chapter);
    }
  }

  /**
  * Update chapter to DB
  *
  */

  updateChapter(chapter: Chapter) {
    this.chapterService.updateChapter(chapter).subscribe(
      () => {
        this.courseUpdated.emit(true);
      }, error => {
        this.setErrorMessage(error.error.message, 4000);
      }
    );
  }

  /**
  * Create chapter to DB
  *
  */
  createChapter(chapter: Chapter) {
    this.chapterService.createChapter(chapter).subscribe(
      () => {
          this._getChapters();
          this.courseUpdated.emit(true);
      }, error => {
        this.setErrorMessage(error.error.message, 4000);
      }
    );
  }

  /**
  * Error Message
  *
  */

  setErrorMessage(message: string, timeOut = 3000) {
    this.error = message;
    window.scroll({
      top: 70,
      left: 0,
      behavior: 'smooth'
    });
    setTimeout(() => {
      this.error = null;
    }, timeOut);
  }

  /**
  * Update all chapters after Re Ordering
  *
  */

  updateMultiChapters(chapters: any) {
    this.chapterService.updateMultiChapters(chapters).subscribe(
      () => {
        this._getChapters();
        this.courseUpdated.emit(true);
      }, error => {
        this.setErrorMessage(error.error.message, 4000);
      }
    );
  }

  /**
  * Save new chapter to DB
  *
  */

removeNullId(listArray:any,removeId:any){
  const res = listArray.filter(obj => obj.id !== removeId);
  this.updateMultiChapters(res);
}

        checkDragValidation(){
          const chapters = this.courseContentForm.value.chapters;
          if (this.courseContentForm.invalid) {
            this.editChapterMode=true;
          }else{
            this.editChapterMode=false;
          }
          for (let index = 0; index < chapters.length; index++) {
            if (!chapters[index].id) {
              this.editChapterMode=true;
            }else{
              this.editChapterMode=false;
            }
          }

        }

  createCourseContent() {
    const chapters = this.courseContentForm.value.chapters;
    if (this.courseContentForm.invalid) {
      this.setErrorMessage('Please fill all the required field first!',5000);
     return;
    }

    this.courseContentForm.markAllAsTouched();
    for (let index = 0; index < chapters.length; index++) {
        chapters[index].sortNumber = index + 1; 
    }
    this.updateMultiChapters(chapters);
  }

 
  toggle(item, i) {
    item.shown = !item.shown;
   item.shown?this.editChapterMode = true:this.editChapterMode=false;  
    this.current = i;
  }
  /**
   * Go to next step
   */
   courseStepperNext() {
    this.courseWizardStepper.to(4);
  }



  courseStepperPrevious() {
    this.courseWizardStepper.to(2);
  }



    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
      this.subscription.unsubscribe();
      this.dragulaService.destroy(`chapters-list`);

    }
  
  
}
