import { Component, OnInit, OnDestroy, ViewEncapsulation, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { locale as english } from 'app/layout/page_translate/en';
import { locale as malay } from 'app/layout/page_translate/my';
import { locale as arabic } from 'app/layout/page_translate/ar';
import { CoreTranslationService } from '@core/services/translation.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import * as snippetDatatable from '@core/components/datatables.snippetcode';
import { ColumnMode, DatatableComponent } from '@swimlane/ngx-datatable';
import { ToastrService } from 'ngx-toastr';
import Swal from 'sweetalert2';
import { InstructorDashboardService } from '../instructor-dashboard.service';
import { Router } from '@angular/router';

@Component({
  selector: 'instructor-courses-datatable',
  templateUrl: './instructor-courses-datatable.component.html',
  encapsulation: ViewEncapsulation.None,
  host: { class: 'ecommerce-application' }
})
export class InstructorCoursesDatatableComponent implements OnInit {
  // Input Decorotor
  @Input() instructor:any;
  @Output() coursesCountEmit = new EventEmitter<Number>();


  // public
  public contentHeader: object;
  public data: any;
  public toggleMenu = true;
  public Monthly = false;
  public toggleNavbarRef = false;
  public loadMoreRef = false;
  private tempData = [];
  public kitchenSinkRows: any;
  public basicSelectedOption: number = 10;
  public ColumnMode = ColumnMode;
  public error:string;
  public coreConfig: any;
  public courses: any;
  public isRTL:boolean;

  public _snippetCodeKitchenSink = snippetDatatable.snippetCodeKitchenSink;

  // private
  private _unsubscribeAll: Subject<any>;
  @ViewChild(DatatableComponent) table: DatatableComponent;
  /**
   
  /**
   * Instructor
   *
   * @param {InstructorDashboardService} _insturctorService
   */
   constructor(
    private _insturctorService: InstructorDashboardService,
     private toastr: ToastrService,
     private router:Router,
     ) {
    this._unsubscribeAll = new Subject();
  }

  // Public Methods
  filterUpdate(event) {
    const val = event.target.value.toLowerCase();
    // filter our data
    const temp = this.tempData.filter(function (d) {
      return d.title.toLowerCase().indexOf(val) !== -1 || !val;
    });
    // update the rows
    this.kitchenSinkRows = temp;
    this.table.offset = 0;
  }

 
  // Lifecycle Hooks
  // -----------------------------------------------------------------------------------------------------
  ngOnInit(): void {
    this.coreConfig = JSON.parse(localStorage.getItem('config'));
    this.isRTL = this.coreConfig.app.appLanguage=='ar' &&  this.coreConfig.layout.type=='horizontal';
    this._getCourses(this.instructor);

  }

     /**
   * Get instructor course info
   */ 
      private _getInstructorInfo(instructorId:string){
        this._insturctorService.getInstructorInfo(instructorId).pipe(takeUntil(this._unsubscribeAll))
        .subscribe(response => {
          this.coursesCountEmit.emit(response.instructor.coursesCount);
        });
      }
   /**
   * Get courses count
   */ 
    private _getCourses(instructorId:string){
      this._insturctorService.getCourses(instructorId).pipe(takeUntil(this._unsubscribeAll))
      .subscribe(response => {
        this.courses = response;
        this.tempData = this.courses;
        this.kitchenSinkRows = this.courses;
  
      });
    }
     /**
   * Delete a course
   */ 
      deleteCourse(instructorId:string,courseId:string) {
    
        Swal.fire({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, delete it!',
          customClass: {
            confirmButton: 'btn btn-primary',
            cancelButton: 'btn btn-danger ml-1'
          }
        }).then((result)=> {
          if (result.value) {
            this._insturctorService.deleteCourse(instructorId,courseId).subscribe(
              () => {
                Swal.fire({
                  title: 'Deleted!',
                  text: 'Your file has been deleted.',
                  icon: 'success',
                  customClass: {
                    confirmButton: 'btn btn-success'
                  }
                });
                this._getCourses(instructorId);
                this._getInstructorInfo(instructorId);
              },
              error=> {
                this.toastr.error(error.error.message, 'Error!', {
                  toastClass: 'toast ngx-toastr',
                  closeButton: true
                });
                Swal.fire({
                  title: 'Error!',
                  text: this.error,
                  icon: 'error',
                  customClass: {
                    confirmButton: 'btn btn-success'
                  }
                });
                return false;
              }
            );
  
       }
      else{
       Swal.fire({
        title: 'Cancelled',
        text: 'Course has not been deleted',
        icon: 'error',
        customClass: {
          confirmButton: 'btn btn-success'
        }
      })
       }
      });
    }
   /**
   * On destroy
   */
    ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
    }
   
    editCourse(courseId:string){
      this.router.navigateByUrl(`instructor/update-course/${courseId}`);

    }
}
