import { Component, OnInit, OnDestroy, Input, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgSelectComponent } from '@ng-select/ng-select';
import { Assignment } from 'app/models/Assignment';
import { Course } from 'app/models/course';
import { AssignmentService } from 'app/services/assignment.service';
import { ChapterService } from 'app/services/chapter.service';
import { CourseService } from 'app/services/course.service';

import Stepper from 'bs-stepper';

import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import Swal from 'sweetalert2';
import { LectureAssignmentFormComponent } from './assignment-form/lecture-assignment-form.component';


@Component({
  selector: 'instructor-course-task',
  templateUrl: './instructor-course-task.component.html'
})

export class InstructorcourseTaskComponent implements OnInit, OnDestroy {

  @ViewChild('lecturesList') lecturesList: NgSelectComponent;
  @ViewChild(LectureAssignmentFormComponent, {static: false}) childAssignmentFormRef: LectureAssignmentFormComponent;
  
  private readonly onDestroy: Subject<any> = new Subject<any>();

  subscription = new Subscription();
  lectures=[];

  // public
  public error:string;

  current: any;
  selectedLecture:any;
  taskAssignmentForm: FormGroup;
   currentAssignmentRow:Assignment;

   public showAssignmentForm:boolean = false;
   assignmentEditMode:boolean =false;

  editmode = false;
  isSubmitted = false;
  assignmentCount = 0;


  public activeTab:string = 'assignment';
  public isInputUpdated = false;

  @Input() appLanguage:string;
  @Input() courseData:FormData;
  @Input() course:Course;

  @Input() chapters:[];

  // private
  private _unsubscribeAll: Subject<any>;
// private
private courseWizardStepper: Stepper;
  // private


  constructor(
    private route:ActivatedRoute,
    private formBuilder: FormBuilder,
    private _assignmentService: AssignmentService,
    private toastr: ToastrService,
    private _chapterService:ChapterService,
    private changeDetector: ChangeDetectorRef,
    private _courseSerivce:CourseService,
    private router:Router,

  ) {
    this._unsubscribeAll = new Subject();

  }

  /**
 * On init --------------------------------------------------------
 */

  ngOnInit(): void {

        this.taskAssignmentForm = this.formBuilder.group({
          assignments: this.formBuilder.array([]),
       })

   this.courseWizardStepper = new Stepper(document.querySelector('#stepper1'), {});
   
  }


  private _getAssignments() {

    this._assignmentService.getAssignments(this.selectedLecture._id).subscribe(assignments => {
      this.setAssignmentDefaultData(assignments);
  
      this.assignmentCount = assignments.length;
     })  
  }

//----------- check on Navigate TAB if there is any updates

navChanged(event:any){
  if (this.isInputUpdated) {
    event.preventDefault();
    this.cancelForm(true,event.nextId);
  }
}
  
destroyFormsChild() {
  if (this.childAssignmentFormRef) 
    this.childAssignmentFormRef.ngOnDestroy();
}

  /**
   * Hide any open forms after create or update
   */
   hideFormsChild(){
    this.isInputUpdated = false;
    this.currentAssignmentRow = null;
    this.showAssignmentForm = false;
    this.assignmentEditMode = false; 
  }

  /**
   * --------------------- Get from OutPut children
   
 * On item updated refresh data from DB ----------------------------
 */

   isItemUpdatedSuccess(isItemUpdatedSuccess:boolean) {
    if (isItemUpdatedSuccess) {
      this._getAssignments();
        this.isInputUpdated = false;
        this. hideFormsChild();
      }   
}

 /**
  * Check if any updates before navigate to new Form
  */
  checInputUpdates(isAnyInputUpdated:boolean){
    this.isInputUpdated = isAnyInputUpdated;
  }



/**
 * check if canceled button was clicked in the child form
 * @param isCancelBtnClicked 
 */
 cancelForm(isCancelBtnClicked:boolean,currentTab='') {
  if (this.isInputUpdated) {
      Swal.fire({
        title: 'Do you want to ignore the current updates?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, ignore updates!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-danger ml-1'
        }
      }).then((result) => {
        if (result.value) { //----------- if yes ignore update and go to next tab
         
          if (isCancelBtnClicked) { 
            /** Read the output from the child, this event
              always send but we want to check if clicked true */
              
           this.destroyFormsChild();
           this.hideFormsChild();
                     }  
            if(currentTab) {
                      this.activeTab = currentTab;
                      this.isInputUpdated = false;
                      return;
                    }  
             }

           else  
           
           {
            this.isInputUpdated = true;
            return;
          }
                 
        }
      );
    }
    else // Just cancel it as there is no update and don't show warning message
    {
      if (isCancelBtnClicked) { 
      this.destroyFormsChild();
      this.hideFormsChild();
      }
    }
  }
  

  _changeChapter(chapterId:any){
    this.lecturesList.handleClearClick();
    this._getLectures(chapterId);
  }


updatCurrentLecture(event){
  this.selectedLecture = event?event:null;
  this._getAssignments();

}


  _getLectures(chapterId:any){
    this._chapterService.getLectures(chapterId).pipe(takeUntil(this._unsubscribeAll))
    .subscribe(response => {
      this.lectures=[];
      this.lectures = response;     
  });

  }



    //----------------- Assignment form 
    openAssignmentForm() {
      this.currentAssignmentRow = null;
      this.showAssignmentForm = true;
      this.assignmentEditMode = false;
      this.changeDetector.detectChanges();
      this.destroyFormsChild();
    }

 //----------- Toggle Assignment
 toggleEditAssignmentForm(assignment:any){
  if(assignment?.id != this.currentAssignmentRow?.id)
  {
    this.cancelForm(true);
    this.destroyFormsChild();
    this.changeDetector.detectChanges();
    this.currentAssignmentRow = assignment;
    this.showAssignmentForm = true;
    this.assignmentEditMode = true;
  }
}



  /**
  * Defaul requirement data
  */

   setAssignmentDefaultData(assignments) {
    this.deleteAll();
    for (let index = 0; index < assignments.length; index++) {
      console.log(assignments[index].isPublished);
      this.addAssignment(assignments[index]);
    }
  }

  addAssignment(assignment: Assignment) {
    let assignments = this.taskAssignmentForm.get('assignments') as FormArray;
    assignments.push(this.createAssignmentArray(assignment));
 
  }



    /**
  * Create new Asset item from db to formArray
  */

     createAssignmentArray(assignment: Assignment) {
      return this.formBuilder.group({
        id: assignment._id,
        lecture:assignment.lecture,
        description: [assignment.description, [Validators.required]],

        fileType:assignment.fileType,
        
        uploadedFile:assignment.uploadedFile,
        uploadedFileName:assignment.uploadedFileName,
        encodedFileName:assignment.encodedFileName,
        uploadedFileSize:assignment.uploadedFileSize,
        uploadedFileType:assignment.uploadedFileType,

        isRequired:assignment.isRequired,
        isPublished:assignment.isPublished
      });
    }


    deleteAssignment(AssignmentId: string) {
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-danger ml-1'
        }
      }).then((result) => {
        if (result.value) {
          this._deleteAssignment(AssignmentId);
  
          Swal.fire({
            title: 'Deleted!',
            text: 'Assignment has been deleted.',
            icon: 'success',
            customClass: {
              confirmButton: 'btn btn-success'
            }
          });
        }
        else {
          Swal.fire({
            title: 'Cancelled',
            text: 'Assignment has not been deleted',
            icon: 'error',
            customClass: {
              confirmButton: 'btn btn-success'
            }
          })
         }
        }
      );
    }
  
  /**
  * Delete Asset from DB
  *
  */

   private _deleteAssignment(AssignmentId) {
    this._assignmentService.deleteAssignment(AssignmentId).subscribe(
      () => {  
        this._getAssignments();
       },

      () => {
        this.toastr.error('Assignment is not deleted!', 'Error!', {
          toastClass: 'toast ngx-toastr',
          closeButton: true
        });
        return false;
      }
    );
  }
   /**
  * Delete all assignments to reset
  */

    deleteAll() {
      while (this.taskAssignmentForm.get('assignments')['controls'].length !== 0) {
        (<FormArray>this.taskAssignmentForm.controls['assignments']).removeAt(0);
      }
    }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();

    }

    skipToCourses(){
      this.router.navigateByUrl(`instructor/dashboard`);
    }
    coursePublish(isPublished:boolean){
      this.route.params.subscribe(params => {
        if(params.id){
        
            this._courseSerivce.updateDraftCourse(this.courseData,params.id,isPublished).subscribe(
              () => {
                this.router.navigateByUrl(`instructor/dashboard`);
              }, error =>{
                window.scroll({ 
                  top: 100, 
                  left: 0, 
                  behavior: 'smooth' 
              });
               this.error = error.error.message;
              }
            );
          } 
      })
    }
    

    courseStepperPrevious(){
      this.courseWizardStepper.to(3);
    }
}
