import { Component, OnInit, OnDestroy, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { locale as english } from 'app/layout/page_translate/en';
import { locale as malay } from 'app/layout/page_translate/my';
import { locale as arabic } from 'app/layout/page_translate/ar';
import { CoreTranslationService } from '@core/services/translation.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { FlatpickrOptions } from 'ng2-flatpickr';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { User } from 'app/models';
import * as moment from 'moment/moment';
import { Arabic } from "flatpickr/dist/l10n/ar.js";
import { ActivatedRoute, Router } from '@angular/router';
import { AccountSettingsService } from 'app/services/account-settings.service';

@Component({
  selector: 'app-account-settings',
  templateUrl: './account-settings.component.html',
  styleUrls: ['./account-settings.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AccountSettingsComponent implements OnInit, OnDestroy {

  // public
  public contentHeader: object;
  public data: any;
  public coreConfig: any;
  public isRTL:boolean;
  public submitted = false;
  public error = '';
  public loading = false;
  public dobDate:string;
  public returnUrl: string;
  public instructorGeneralForm: UntypedFormGroup;


  public birthDateOptions: FlatpickrOptions = {
    altInput: true,
    maxDate:'01-01-2008',
    minDate:'01-01-1935',
    altFormat: 'F j, Y',
    dateFormat: 'Y-m-d',
    disableMobile: true
  };
  public passwordTextTypeOld = false;
  public passwordTextTypeNew = false;
  public passwordTextTypeRetype = false;
  public avatarImage: string | ArrayBuffer;
  public oldAvatarImage:string;
  countries = [];


  // private
  private _unsubscribeAll: Subject<any>;

  /**
   * Constructor
   *
   * @param {AccountSettingsService} _accountSettingsService
   */
  constructor(
    private _accountSettingsService: AccountSettingsService,
    private _coreTranslationService: CoreTranslationService,
    private _formBuilder: UntypedFormBuilder,
    private cdref: ChangeDetectorRef ,
    private _route: ActivatedRoute,
    private _router:Router,
    ) {
      
    this._unsubscribeAll = new Subject();
    this._coreTranslationService.translate(english, malay, arabic);
  }

  ngAfterContentChecked() {
    this.cdref.detectChanges();
     }


  /**
   * On init
   */
   ngOnInit() {
    this.coreConfig = JSON.parse(localStorage.getItem('config'));
    this.isRTL = this.coreConfig.app.appLanguage=='ar' &&  this.coreConfig.layout.type=='horizontal'; 
    if(this.isRTL){
      this.birthDateOptions.locale=Arabic,
      this.birthDateOptions.altFormat= 'j F Y'
    }

    this._getCountries();
    this._initGeneralForm();
    this._setGeneralValues();

    this.instructorGeneralForm.patchValue({
      gender: this.data.general.gender,
      student_join: this.data.notification.student_join,
      exercise_answer: this.data.notification.exercise_answer,
      course_comment: this.data.notification.course_comment,
      assignment_answer: this.data.notification.assignment_answer
    });
    this.returnUrl =this._route.snapshot['_routerState'].url || '/';
    // content header
    this.contentHeader = {
      headerTitle: 'account_setting',
      actionButton: true,
      breadcrumb: {
        type: '',
        links: [
          {
            name: 'home',
            isLink: true,
            link: '/'
          },
          {
            name: 'account_setting',
            isLink: false
          }
        ]
      }
    };
  }

private _setGeneralValues(){
  this._accountSettingsService.onSettingsChanged.pipe(takeUntil(this._unsubscribeAll)).subscribe(response => {
    this.data = response;
    this.avatarImage = this.data.avatar;
    this.oldAvatarImage = this.data.avatar;
    this.generalForm.name.setValue(response.name);
    this.generalForm.email.setValue(response.email);
    this.generalForm.country.setValue(response.general.country?response.general.country:'');
    this.generalForm.phone.setValue(response.general.phone?response.general.phone:'');
    this.generalForm.website.setValue(response.general.website?response.general.website:'');
    this.generalForm.jobTitle.setValue(response.general.jobTitle?response.general.jobTitle:'');
    this.generalForm.company.setValue(response.general.company?response.general.company:'');
    this.generalForm.bio.setValue(response.general.bio?response.general.bio:'');
    this.generalForm.country.setValue(response.general.country?response.general.country:'');
    this.generalForm.dob.setValue(response.general.dob?response.general.dob:'');
    this.dobDate = moment(response.general.dob).format('YYYY-MM-DD');
    this.generalForm.avatar.setValidators([]);
    this.generalForm.avatar.updateValueAndValidity();


    this.generalForm.twitter.setValue(response.social.twitter?response.social.twitter:'');
    this.generalForm.facebook.setValue(response.social.facebook?response.social.facebook:'');
    this.generalForm.linkedin.setValue(response.social.linkedin?response.social.linkedin:'');
    this.generalForm.instagram.setValue(response.social.instagram?response.social.instagram:'');

    this.generalForm.student_join.setValue(response.notification.student_join);
    this.generalForm.exercise_answer.setValue(response.notification.exercise_answer);
    this.generalForm.course_comment.setValue(response.notification.course_comment);
    this.generalForm.assignment_answer.setValue(response.notification.assignment_answer);

   });
}

  private _initGeneralForm(){
    this.instructorGeneralForm = this._formBuilder.group({
      name: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]],
      dob: ['', [Validators.required]],
      gender: ['', [Validators.required]],
      country: [''],
      phone: [''],
      company: [''],
      jobTitle: ['', [Validators.required]],
      website: [''],
      bio: [''],
      avatar:[''],


      twitter: [''],
      facebook: [''],
      linkedin: [''],
      instagram:[''],

      student_join:[''],
      exercise_answer: [''],
      course_comment: [''],
      assignment_answer: ['']

    });
  }

  private _getCountries() {
    this.countries = this._accountSettingsService.getCountries(this.isRTL?'ar':'en');
  }


  // Public Methods
  // -----------------------------------------------------------------------------------------------------
  get generalForm() {
    return this.instructorGeneralForm.controls;
  }

   /**
  * On Submit
  */
 onGeneralSubmit() {
  this.submitted = true;

  // stop here if form is invalid
  if (this.instructorGeneralForm.invalid) {
   window.scroll({ 
    top: 100, 
    left: 0, 
    behavior: 'smooth' 
});
    return;
  }

  const generalInfo = new FormData();

  Object.keys(this.generalForm).map((key)=>{
    generalInfo.append(key,this.generalForm[key].value);
  });


  this._updateInstructorGeneral(generalInfo,this.data._id);

}

private _updateInstructorGeneral(generalInfo: FormData,instructor_id:string){
  this._accountSettingsService.updateGeneral(generalInfo,instructor_id).subscribe(
    (user: User) => {
      this.loading = true;
      this.data = user;
      localStorage.removeItem('currentUser');
      localStorage.setItem('currentUser', JSON.stringify(user));
      
      //this._router.navigate([this.returnUrl]);

      window.location.reload();

    }, error =>{
      window.scroll({ 
        top: 100, 
        left: 0, 
        behavior: 'smooth' 
    });
     this.error = error.error.message;
    }
  );
}



  /**
   * Toggle Password Text Type Old
   */
  togglePasswordTextTypeOld() {
    this.passwordTextTypeOld = !this.passwordTextTypeOld;
  }

  /**
   * Toggle Password Text Type New
   */
  togglePasswordTextTypeNew() {
    this.passwordTextTypeNew = !this.passwordTextTypeNew;
  }

  /**
   * Toggle Password Text Type Retype
   */
  togglePasswordTextTypeRetype() {
    this.passwordTextTypeRetype = !this.passwordTextTypeRetype;
  }

  /**
   * Upload Image
   *
   * @param event
   */


  uploadImage(event:any){
    const file = event.target.files[0];
    if (file) {
      this.instructorGeneralForm.patchValue({avatar:file});
      this.instructorGeneralForm.get('avatar').updateValueAndValidity();
      const fileReader = new FileReader();
      fileReader.onload = () =>{
        this.avatarImage = fileReader.result
      };
      fileReader.readAsDataURL(file);
    }
    }

  
  resetImage(){
    this.avatarImage = this.oldAvatarImage;
    this.instructorGeneralForm.patchValue({avatar:''});
  }
  // Lifecycle Hooks
  // -----------------------------------------------------------------------------------------------------

  onNotificationChange(event){
    let event_name = event.target.name;
    let event_status = event.target.checked;

    this.instructorGeneralForm.patchValue({event_name:event_status});
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next();
    this._unsubscribeAll.complete();
  }
}
