import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Input, Component, OnInit, ViewChild, SimpleChanges, EventEmitter, Output, ElementRef } from '@angular/core';
import {FormGroup, NgForm } from '@angular/forms';

import { AssetService } from 'app/services/asset.service';
import { FileUploader } from 'ng2-file-upload';
import { Subject } from 'rxjs';

import { CommonService } from 'app/services/common.service';
import { ToastrService } from 'ngx-toastr';
import { Lecture } from 'app/models/lecture';



@Component({
  selector: 'asset-docs-form',
  templateUrl: './asset-docs-form.component.html'
})
export class AssetDocsFormComponent implements OnInit {
  @ViewChild('fileInputVariable') fileInputVariable: ElementRef;
  @ViewChild('assetEditDocumentForm') assetEditDocumentForm: NgForm;

  public currentRow: any;
  public error:string;
  public pageCount = 0;
  public uploadedFile:string| ArrayBuffer;
  isSubmitted = false;
  form:FormGroup;
  currentAssetId:string;

  progress: number = 0;

  public editMode=false;

  public inputUpdated=false;
  public loading:boolean = false;


  // Input Decorator
   @Input() lecture: Lecture;
   @Input() asset: any;
   @Output() isUpdatedSuccess = new EventEmitter<boolean>();
   @Output() isCancelNewDocument = new EventEmitter<boolean>();
   @Output() isInputUpdated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;

  public uploader: FileUploader = new FileUploader({
    isHTML5: true,
    queueLimit:2,
    disableMultipart:true,
    maxFileSize:100 * 1024 * 1024,
    allowedMimeType: 
    [
      "application/pdf",
      "text/csv",
      "application/msword",
      "application/vnd.ms-powerpoint",
      "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      "application/vnd.openxmlformats-officedocument.presentationml.presentation",
    ]
  ,
  });
  /**
   * Constructor
   *
   */
  constructor(
    private _assetSerivce:AssetService,
    private _commonService:CommonService,
    private toastr: ToastrService
    ) { 
      this._unsubscribeAll = new Subject();
    }
  /**
   * On Initial
   *
   */

    ngOnInit(): void {

      this.isUpdatedSuccess.emit(false);
      this.isCancelNewDocument.emit(false);
      this.isInputUpdated.emit(false);
    
      this._initForm();
     if(this.asset)
     {
      this.editMode = true;
      this.currentRow = this.asset;
      this.currentAssetId=this.asset.id;
      this._setFormValues(this.asset);
     }
    }
// ----- /End initiate

inputChange(event:any){
this.isInputUpdated.emit(true);
this.inputUpdated = true;
}

countChange(value) {
  this.pageCount = value;
  this.form.patchValue({pageCount:this.pageCount});
  this.isInputUpdated.emit(true);
  this.inputUpdated = true;
}

   /*
   Cancel any updates
   */ 
    cancelNewDocument(){
      this.deleteUploadedFile();
      this.isCancelNewDocument.emit(true);
    }

    /* 
    Scroll to form
    */

    ngAfterViewInit() {
      window.scrollTo(1900, 540);
    }

    /* 
    Reset Uploaded file before uploading process
    */
  

  deleteUploadedFile(){
    this.fileInputVariable.nativeElement.value = "";
    this.uploader.destroy();
    this.uploader.clearQueue();
   }


    /* 
    Reset Form With Default Values
    */
   resetFormWithDefaultValues() {
    this.isInputUpdated.emit(false);
    this.inputUpdated = false;
    this.pageCount = this.currentRow.pageCount;
    this.assetEditDocumentForm.resetForm(this.currentRow);  
    this.deleteUploadedFile();
  }

      /* 
    Initiate form
    */

      private _initForm(){
        let section = 'lecture_explenation';
        let fileType = 'docs';
        this.form = this._assetSerivce._initForm(this.currentAssetId,this.lecture,section,fileType);
      }   

  /**
   * set value if this in update mode
   * 
   * @param asset 
   */    
   
   private _setFormValues(asset:any){
        
        this.assetDocumentForm.description.setValue(asset.description);

        this.assetDocumentForm.uploadedFile.setValue(asset.uploadedFile);
        this.assetDocumentForm.uploadedFileName.setValue(asset.uploadedFileName);
        this.assetDocumentForm.encodedFileName.setValue(asset.encodedFileName);
        this.assetDocumentForm.uploadedFileSize.setValue(asset.uploadedFileSize);
        this.assetDocumentForm.uploadedFileType.setValue(asset.uploadedFileType);

        this.assetDocumentForm.duration.setValue(asset.duration);
        this.pageCount = asset.pageCount;
        this.assetDocumentForm.hours.setValue(asset.hours);
        this.assetDocumentForm.minutes.setValue(asset.minutes);
        this.assetDocumentForm.seconds.setValue(asset.seconds);

        this.assetDocumentForm.isExternal.setValue(asset.isExternal);
        this.assetDocumentForm.isPublished.setValue(asset.isPublished);
        this.assetDocumentForm.uploadedFile.setValidators([]);
        this.assetDocumentForm.uploadedFile.updateValueAndValidity();
      }


  /**
   * On Submit
   *
   */
   assetDocumentFormSubmit() {

    this.isUpdatedSuccess.emit(false);
    this.isSubmitted = true;

    if (this.form.invalid) return;

    const assetDocumentFormData = new FormData();
    Object.keys(this.assetDocumentForm).map((key)=>{
      assetDocumentFormData.append(key,this.assetDocumentForm[key].value);
     });
    
    this._updateAsset(assetDocumentFormData);
  }


  _updateAsset(assetDocumentFormData: FormData) {

   this._assetSerivce.updateAsset(
      assetDocumentFormData,
      this.currentAssetId
    )
    .subscribe((event: HttpEvent<any>) => {

      switch (event.type) {
   
        case HttpEventType.Sent:
          break;
        case HttpEventType.ResponseHeader:
          break;
        case HttpEventType.UploadProgress:
          this.progress = Math.round(event.loaded / event.total * 100);
          this.loading = true;

          break;
        case HttpEventType.Response:

          this.currentRow = event.body;
          this.asset = event.body;
          this.loading = false;

          this.isUpdatedSuccess.emit(true);
          this.isInputUpdated.emit(false);
          
          this.toastr.success(this.editMode?'Content updated successfuly!':'Content created successfuly!', 'Success!', {
            toastClass: 'toast ngx-toastr',
            closeButton: true
          });
      setTimeout(() => {
            this.progress = 0;
          }, 1500);
          this.deleteUploadedFile();
      }
    },  (err) => {
      this.toastr.error(this.editMode?'Content wasn\'t updated!':'Content wasn\'t created!', 'Error!', {
        toastClass: 'toast ngx-toastr',
        closeButton: true
      });
    this.setErrorMessage(this.editMode?'Update failed, something went wrong':'Create document failed, something went wrong');
    this.loading = false; 
    });
  }


  getPageCount(event) {
    const fileReader = new FileReader();
    fileReader.readAsBinaryString(event.target.files[0]);
    fileReader.onloadend = () => {
        const count = (fileReader.result as string).match(/\/Type[\s]*\/Page[^s]/g).length;
        this.form.patchValue({pageCount:count}); 
        this.pageCount = count;
    }
  }


  uploadDocument(event:any){

    if (this.uploader.queue.length>1) {
      this.uploader.queue[0].remove();
    }
    const file = event.target.files[0];

    this.uploader.onWhenAddingFileFailed = (file) => {
    this.error = "File type is not supported";
    this.deleteUploadedFile();
    }

    if (file) {
   if(this.uploader.queue[0]?.file?.type =='application/pdf')
    { this.getPageCount(event);}



      let uploadedFileName =this.uploader.queue[0]?.file?.name;
      let uploadedFileSize = (this.uploader.queue[0]?.file?.size / 1024 / 1024).toFixed(2)+' MB';
      let uploadedFileType =this.uploader.queue[0]?.file?.type;
  
      this.form.patchValue({uploadedFile:file});
      this.form.patchValue({uploadedFileName:uploadedFileName});
      this.form.patchValue({uploadedFileSize:uploadedFileSize});
      this.form.patchValue({uploadedFileType:uploadedFileType});
     
      this.form.get('uploadedFile').updateValueAndValidity();
      const fileReader = new FileReader();
      fileReader.onload = () =>{
        this.uploadedFile = fileReader.result
      };
      fileReader.readAsDataURL(file);
    }
    }
 

 
  get assetDocumentForm(){
    return this.form.controls;
  }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
    }

    setErrorMessage(message: string, timeOut = 3000) {
      this.error = message;
    this.ngAfterViewInit();
      setTimeout(() => {
        this.error = null;
      }, timeOut);
    }
  
  
}
