import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { FormArray, FormBuilder,FormGroup, Validators } from '@angular/forms';
import { Course } from 'app/models/course';
import { CourseService } from 'app/services/course.service';
import Stepper from 'bs-stepper';
import { Subject } from 'rxjs';


@Component({
  selector: 'instructor-course-target-student',
  templateUrl: './instructor-course-target-student.component.html'
})
export class InstructorCourseTargetStudentComponent implements OnInit, OnDestroy {
  // public

  public error:string;
  form:FormGroup;
  targetStudentForm:FormGroup;


  @Input() appLanguage:string;
  @Input() course:any;


  // private
private _unsubscribeAll: Subject<any>;
// private
private courseWizardStepper: Stepper;

  constructor(
    private formBuilder:FormBuilder,
    private _courseSerivce:CourseService
     ) {
    this._unsubscribeAll = new Subject();
   

  }

  //------------------------ Requirement ------------------------

  /**
   * Delete Requirement Item
   *
   * @param id
   */
   deleteReqItem(id:any) {
     const items = this.targetStudentForm.get('requirements')['controls'];
    for (let i = 0; i < items.length; i++) {
      if (items.indexOf(items[i]) === id) {
        (<FormArray>this.targetStudentForm.controls['requirements']).removeAt(i);  
       
      }
    }
  }

   /**
   * Reset to the previous requirements data
   *
   */
  
   resetReqItems(){
    const items = this.targetStudentForm.get('requirements')['controls'];
    while(items.length > 0) {
      items.pop();
  }
    for (let index = 0; index < this.course.requirements.length; index++) {
      this.addRequirement(this.course.requirements[index].reqItem);
     }
  }
  
    /**
   * Defaul requirement data
   */

     setReqDefaultData(){
      for (let index = 0; index < this.course.requirements?.length; index++) {
        this.addRequirement(this.course.requirements[index].reqItem);
       }

      if (!this.course.requirements) {
        this.addRequirement('');
      }

    if (this.course.requirements?.length==0) {
      this.addRequirement('');
    }

    }
  
    /**
   * Add new Requirement item
   */

addRequirement(reqItem =""){
  let requirements = this.targetStudentForm.get('requirements') as FormArray;
  requirements.push(this.formBuilder.group({
    reqItem : [reqItem, [Validators.required]]
  }));
}
  
  //------------------------ / End Requirement ------------------------

    /**
   * On init
   */
  ngOnInit(): void {
    this.targetStudentForm = this.formBuilder.group({
      requirements : this.formBuilder.array([]),
      learns : this.formBuilder.array([]),
    })

    this.setReqDefaultData();
    this.setLearnDefaultData();

    this.courseWizardStepper = new Stepper(document.querySelector('#stepper1'), {});

  }


  //------------------------ Learns ------------------------

    /**
   * Defaul learn data
   */

setLearnDefaultData(){
  for (let index = 0; index < this.course.learns?.length; index++) {
    this.addLearn(this.course.learns[index].learnItem);
   }
   
   if (!this.course.learns) {
    this.addLearn('');
  }

if (this.course.learns?.length==0) {
  this.addLearn('');
}
}

    /**
   * Add new Learn item
   */

addLearn(learnItem =""){
  let learns = this.targetStudentForm.get('learns') as FormArray;
  learns.push(this.formBuilder.group({
    learnItem : [learnItem, [Validators.required]]
  }));
}
  
  /**
   * Delete Requirement Item
   *
   * @param id
   */
   deleteLearnItem(id:any) {
    const items = this.targetStudentForm.get('learns')['controls'];
   for (let i = 0; i < items.length; i++) {
     if (items.indexOf(items[i]) === id) {
       (<FormArray>this.targetStudentForm.controls['learns']).removeAt(i);  
      
     }
   }
 }

      /**
   * Reset to the previous learns data
   *
   */
  
     resetLearnItems(){
      const items = this.targetStudentForm.get('learns')['controls'];
      while(items.length > 0) {
        items.pop();
    }
      for (let index = 0; index < this.course.learns.length; index++) {
        this.addLearn(this.course.learns[index].learnItem);
       }
    }
  
  //---------------------- End Learn -------------------  

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next();
    this._unsubscribeAll.complete();
  }


    createTargetStudentInfo(){

      if (this.targetStudentForm.invalid) {
        console.log('error');
        return;
      }
      this.targetStudentForm.markAllAsTouched();
   
      while(this.course.requirements?.length > 0) {
        this.course.requirements.pop();
    }

    while(this.course.learns?.length > 0) {
      this.course.learns.pop();
  }  

    this.course.learns = this.targetStudentForm.value.learns;

    this.course.requirements = this.targetStudentForm.value.requirements;

    this._courseSerivce.updateCourseItem(this.course,this.course._id).subscribe(
      (course: Course) => {
        this.courseWizardStepper.to(3);
      }, error =>{
        window.scroll({ 
          top: 100, 
          left: 0, 
          behavior: 'smooth' 
      });
       this.error = error.error.message;
      }
    );

    }

    courseStepperPrevious(){
      this.courseWizardStepper.to(1);
      //this.router.navigateByUrl(`instructor/update-course/${this.currentCourseId}`);
    }
}
