import { Component, OnInit, OnDestroy, ViewEncapsulation, ViewChild, Input, EventEmitter, Output, SimpleChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgSelectComponent } from '@ng-select/ng-select';
import { Course } from 'app/models/course';
import { Level } from 'app/models/level';
import { CategoryService } from 'app/services/category.service';
import { CourseService } from 'app/services/course.service';
import { PackageService } from 'app/services/package.service';

import Stepper from 'bs-stepper';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InstructorDashboardService } from '../../instructor-dashboard.service';

@Component({
  selector: 'instructor-course-form-details',
  templateUrl: './instructor-course-form-details.component.html',
  encapsulation: ViewEncapsulation.None
})
export class InstructorCourseFormDetailsComponent implements OnInit, OnDestroy {
  // public
  @ViewChild('subCategryList') subCategryList: NgSelectComponent;

  public user:any;
  public tempCoverPhoto:string| ArrayBuffer='assets/images/no-image-course.png';
  public coverPhoto:string| ArrayBuffer='assets/images/no-image-course.png';
  public tempSubCategories = [];
  public subCategories = [];
  public error:string;
  editmode = false;
  isSubmitted = false;
  form:FormGroup;
  currentCourseId:string;

  levels: Level[]; 


  @Input() appLanguage:string;
  @Input() categories:[];
  @Input() course:any;

  @Output() courseData = new EventEmitter<FormData>();
  @Output() newCourse = new EventEmitter<any>();
  @Output() isCourseCreated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;
// private
private courseWizardStepper: Stepper;

  /**
   * Constructor
   *
   * @param {CategoryService} _CategoryService
   */
  constructor(
    private _CategoryService: CategoryService,
    private formBuilder:FormBuilder,
    private route:ActivatedRoute,
    private _courseSerivce:CourseService,
    private _levelSerivce:PackageService,
    private _insturctorService: InstructorDashboardService,
    private router:Router,
     ) {
    this._unsubscribeAll = new Subject();

  }

 
  // Lifecycle Hooks
  // -----------------------------------------------------------------------------------------------------

  /**
   * On init
   */
  ngOnInit(): void {
   this._getLevels();
    this.isCourseCreated.emit(false);
    this.courseWizardStepper = new Stepper(document.querySelector('#stepper1'), {});
    this._insturctorService.onSettingsChanged.pipe(takeUntil(this._unsubscribeAll)).subscribe(response => {
      this.user = response;
     });
    this._initForm();
    //-------- Testing 
    setTimeout(() => {
    //this.courseWizardStepper.to(2);
    }, 10);
// ---------------------
    this.route.params.subscribe(params => {
      if(params.id){
        this.editmode = true;
        this.currentCourseId = params.id;
        this._setFormValues();
      }
    })
    if(this.router.url.includes('create-course')){
      setTimeout(() => {
        this.courseWizardStepper.to(2);
      }, 10);
     }

   }

 
   private _getLevels() {
    this._levelSerivce.getLevels().subscribe(items => {
      this.levels = items;
     })  
  }

      courseDetailsFormSubmit(){
        this.isSubmitted = true;
      if (this.form.invalid) return;
      const courseFormData = new FormData();
      Object.keys(this.courseDetailsForm).map((key)=>{
        console.log(key,this.courseDetailsForm[key].value);
       courseFormData.append(key,this.courseDetailsForm[key].value);
      });

      this.courseData.emit(courseFormData);
      //add or update function
      if(this.editmode)
      this._updateCourseDetails(courseFormData);
      else this._createCourse(courseFormData);
      
      }

      reloadCurrentRoute(currentUrl:any) {
       
        this.router.navigateByUrl(currentUrl, { skipLocationChange: false }).then(() => {
            this.router.navigate([currentUrl]);
            window.location.reload();
        });
    }     



      private _createCourse(courseFormData: FormData){
        this._courseSerivce.createCourse(courseFormData).subscribe(
          (course: Course) => {
            this.course = course;
            this.currentCourseId = course.id;
            this.newCourse.emit(course);
            this.isCourseCreated.emit(true);

        }, error =>{
      
            window.scroll({ 
              top: 100, 
              left: 0, 
              behavior: 'smooth' 
          });
           this.error = error.error.message;
          }
        );
      }   

      private _updateCourseDetails(courseFormData: FormData){
        this._courseSerivce.updateCourse(courseFormData,this.currentCourseId).subscribe(
          (course: Course) => {
            this.course = course;
           this.courseWizardStepper.to(2);
            //this.courseWizardStepper.next();
 
          }, error =>{
            window.scroll({ 
              top: 100, 
              left: 0, 
              behavior: 'smooth' 
          });
           this.error = error.error.message;
          }
        );
      }
      
  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next();
    this._unsubscribeAll.complete();
  }

  _changeCategory(categoryId:any){

    this.subCategryList.handleClearClick();
    this._getSubCategories(categoryId);
  }


   _getSubCategories(categoryId:any){
  
    this._CategoryService.getSubCategory(categoryId).pipe(takeUntil(this._unsubscribeAll))
    .subscribe(response => {
      this.subCategories=[];
      this.tempSubCategories = response;
          this.tempSubCategories.forEach(elements => {
              elements.translate.forEach(translate=>{
                if(translate.lang==this.appLanguage){
                this.subCategories.push
                ({ title: translate.title,
                   id:elements.id})
                }
                });
            });
       
  });

  }
  
    uploadImage(event:any){
      const file = event.target.files[0];
      if (file) {
        this.form.patchValue({coverPhoto:file});
        this.form.get('coverPhoto').updateValueAndValidity();
        const fileReader = new FileReader();
        fileReader.onload = () =>{
          this.coverPhoto = fileReader.result
        };
        fileReader.readAsDataURL(file);
      }
      }

      resetImage(){
        this.coverPhoto = this.tempCoverPhoto;
        this.form.patchValue({coverPhoto:''});
    }

    
    private _initForm(){
      this.form = this.formBuilder.group({
        id:this.currentCourseId,
        instructor:this.user._id,
        title: ['', Validators.required],
        category: ['', Validators.required],
        subCategory: ['', Validators.required],
        level: ['', Validators.required],
        coverPhoto: [''],
        description: [''],
        learns: [''],
        requirements: [''],
        isCoaching: true,
        rating: [''],
        isFree:true,
        price: 0,
        studentsCount: [''],
        isPublished: false,
        isDraft:true,
    

      })
    }
  
    private _setFormValues(){
          if(this.course)
            {
            this._getSubCategories(this.course.subCategory.category.id);
            this.courseDetailsForm.title.setValue(this.course.title);
            this.courseDetailsForm.description.setValue(this.course.description);
            this.courseDetailsForm.level.setValue(this.course.level?._id);
            this.courseDetailsForm.category.setValue(this.course.subCategory.category._id);
            this.courseDetailsForm.subCategory.setValue(this.course.subCategory._id);
       
            this.coverPhoto = this.course.coverPhoto;
            this.tempCoverPhoto = this.course.coverPhoto;

            this.courseDetailsForm.coverPhoto.setValidators([]);
            this.courseDetailsForm.coverPhoto.updateValueAndValidity();
          }
        
       
      }
    
    
    get courseDetailsForm(){
      return this.form.controls;
    }
}
