import { Component, OnInit, OnDestroy, Input, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Asset } from 'app/models/asset';
import { AssetService } from 'app/services/asset.service';
import { ChapterService } from 'app/services/chapter.service';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import Swal from 'sweetalert2';
import { AssetAudioFormComponent } from './audio-component/asset-audio-form.component';
import { AssetDocsFormComponent } from './docs-component/asset-docs-form.component';
import { AssetDownloadableFormComponent } from './downloadable-component/asset-downloadable-form.component';
import { AssetExternalFormComponent } from './external-component/asset-external-form.component';
import { AssetVideoFormComponent } from './video-component/asset-video-form.component';
import { AssetYoutubeFormComponent } from './youtube-component/asset-youtube-form.component';

@Component({
  selector: 'instructor-course-assets',
  templateUrl: './instructor-course-assets.component.html'
})

export class InstructorcourseAssetsComponent implements OnInit, OnDestroy {

  @ViewChild(AssetVideoFormComponent, {static: false}) childVideoRef: AssetVideoFormComponent;
  @ViewChild(AssetAudioFormComponent, {static: false}) childAudioRef: AssetAudioFormComponent;
  @ViewChild(AssetYoutubeFormComponent, {static: false}) childYoutubeRef: AssetYoutubeFormComponent;
  @ViewChild(AssetDocsFormComponent, {static: false}) childDocumentRef: AssetYoutubeFormComponent;

  @ViewChild(AssetExternalFormComponent, {static: false}) childExternalRef: AssetExternalFormComponent;
  @ViewChild(AssetDownloadableFormComponent, {static: false}) childDownloadableRef: AssetDownloadableFormComponent;


  private readonly onDestroy: Subject<any> = new Subject<any>();

  subscription = new Subscription();


  // public
  public error: string;


  courseAssetsForm: FormGroup;
  currentRowAsset:any;
  

  public isInputUpdated = false;
  public activeTab:string = 'video';

  public showVideoForm:boolean = false;
  videoEditMode:boolean =false;
  
  public showAudioForm:boolean = false;
  audioEditMode:boolean =false;

  public showYoutubeForm:boolean = false;
  youtubeEditMode:boolean =false;

  public showDocumentForm:boolean = false;
  docsEditMode:boolean =false;

  public showExternalForm:boolean = false;
  externalEditMode:boolean =false;

  public showDownloadableForm:boolean = false;
  downloadableEditMode:boolean =false;

  

  videofilter= {fileType:'video'};
  audiofilter= {fileType:'audio'};
  youtubefilter= {fileType:'youtube'};
  docsfilter= {fileType:'docs'};
  externalfilter= {fileType:'external'};
  downloadablefilter= {fileType:'downloadable'};

  
  countVideoItem = { count: 0 };
  countAudioItem = { count: 0 };
  countYoutubeItem = { count: 0 };
  countDocumentItem = { count: 0 };
  countExternalItem = { count: 0 };
  countDownloadableItem = { count: 0 };

  @Input() appLanguage: string;
  @Input() public lecture: any;


  // private
  private _unsubscribeAll: Subject<any>;
  // private


  constructor(
    private formBuilder: FormBuilder,
    private chapterService: ChapterService,
    private toastr: ToastrService,
    private changeDetector: ChangeDetectorRef,
    private _assetSerivce:AssetService,
  ) {
    this._unsubscribeAll = new Subject();
  }

  /**
 * On init --------------------------------------------------------
 */

  ngOnInit(): void {
     this.courseAssetsForm = this.formBuilder.group({
              assets: this.formBuilder.array([]),
           })
    this._getAssets();
  }

//----------- check on Navigate TAB if there is any updates

navChanged(event:any){
          if (this.isInputUpdated) {
            event.preventDefault();
            this.cancelForm(true,event.nextId);
          }
  }

  //------- Destroy Video Component ------------
  
  destroyFormsChild() {
    if (this.childVideoRef) 
      this.childVideoRef.ngOnDestroy();
    
    if (this.childAudioRef) 
    this.childAudioRef.ngOnDestroy();

    if (this.childYoutubeRef) 
    this.childYoutubeRef.ngOnDestroy();

    if (this.childDocumentRef) 
    this.childDocumentRef.ngOnDestroy();

    if (this.childExternalRef) 
    this.childExternalRef.ngOnDestroy();

    if (this.childDownloadableRef) 
    this.childDownloadableRef.ngOnDestroy();
    
  }

  /**
   * Hide any open forms after create or update
   */
  hideFormsChild(){
    this.isInputUpdated = false;
    this.currentRowAsset = null;

    this.showVideoForm = false;
    this.videoEditMode = false;

    this.showAudioForm = false;
    this.audioEditMode = false;

    this.showYoutubeForm = false;
    this.youtubeEditMode = false;  

    this.showDocumentForm = false;
    this.docsEditMode = false;
    
    this.showExternalForm = false;
    this.externalEditMode = false;  

    this.showDownloadableForm = false;
    this.downloadableEditMode = false; 
    
  }
  /**
   * --------------------- Get from OutPut children
   
 * On item updated refresh data from DB ----------------------------
 */

   isItemUpdatedSuccess(isItemUpdatedSuccess:boolean) {
         if (isItemUpdatedSuccess) {
             this._getAssets();
             this.isInputUpdated = false;
             this. hideFormsChild();
           }   
    }

 /**
  * Check if any updates before navigate to new Form
  */

  checInputUpdates(isAnyInputUpdated:boolean){
    this.isInputUpdated = isAnyInputUpdated;
  }

/**
 * check if canceled button was clicked in the child form
 * @param isCancelBtnClicked 
 */
  cancelForm(isCancelBtnClicked:boolean,currentTab='') {
    if (this.isInputUpdated) {
        Swal.fire({
          title: 'Do you want to ignore the current updates?',
          text: "You won't be able to revert this!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, ignore updates!',
          customClass: {
            confirmButton: 'btn btn-primary',
            cancelButton: 'btn btn-danger ml-1'
          }
        }).then((result) => {
          if (result.value) { //----------- if yes ignore update and go to next tab
           
            if (isCancelBtnClicked) { 
              /** Read the output from the child, this event
                always send but we want to check if clicked true */
              this.destroyFormsChild();
              this.hideFormsChild();
                       }  
              if(currentTab) {
                        this.activeTab = currentTab;
                        this.isInputUpdated = false;
                        return;
                      }  
               }

             else  
             
             {
              this.isInputUpdated = true;
              return;
            }
                   
          }
        );
      }
      else // Just cancel it as there is no update and don't show warning message
      {
        if (isCancelBtnClicked) { 
          this.destroyFormsChild();
          this.hideFormsChild();
        }
      }
    }
    
/** 
 * --------------- End Outputs ------------------
*/

  //------------------------- Ope forms ---------------------

  //----------------- Video form 
  openVideoForm() {
    this.currentRowAsset = null;
    this.showVideoForm = true;
    this.videoEditMode = false;
    this.changeDetector.detectChanges();
    this.destroyFormsChild();
  }

 // Audio form ------------------

 openAudioForm() {
  this.currentRowAsset = null;
  this.showAudioForm = true;
  this.audioEditMode = false;
  this.changeDetector.detectChanges();
  this.destroyFormsChild();
}

 // Youtube form ------------------

 openYoutubeForm() {
  this.currentRowAsset = null;
  this.showYoutubeForm = true;
  this.youtubeEditMode = false;
  this.changeDetector.detectChanges();
  this.destroyFormsChild();
}



 // Youtube form ------------------

 openDocumentForm() {
  this.currentRowAsset = null;
  this.showDocumentForm = true;
  this.docsEditMode = false;
  this.changeDetector.detectChanges();
  this.destroyFormsChild();
}

 // External form ------------------

 openExternalForm() {
  this.currentRowAsset = null;
  this.showExternalForm = true;
  this.externalEditMode = false;
  this.changeDetector.detectChanges();
  this.destroyFormsChild();
}

openDownloadableForm() {
  this.currentRowAsset = null;
  this.showDownloadableForm = true;
  this.downloadableEditMode = false;
  this.changeDetector.detectChanges();
  this.destroyFormsChild();
}



  //------------------------ On Edit clicked ------------------------
  
  //----------- Toggle Video
  toggleEditVideoForm(asset:any){
    if(asset?.id != this.currentRowAsset?.id)
    {
      this.cancelForm(true);
      this.destroyFormsChild();
      this.changeDetector.detectChanges();
      this.currentRowAsset = asset;
      this.showVideoForm = true;
      this.videoEditMode = true;
    }
  }

  //----------- Toggle Audio

  toggleEditAudioForm(asset){

    if(asset?.id != this.currentRowAsset?.id)
    {
      this.cancelForm(true);
      this.destroyFormsChild();
      this.changeDetector.detectChanges();
      this.currentRowAsset = asset;
      this.showAudioForm = true;
      this.audioEditMode = true;
    }
  }
  //----------- Toggle Youtube

  toggleEditYoutubeForm(asset){

    if(asset?.id != this.currentRowAsset?.id)
    {
      this.cancelForm(true);
      this.destroyFormsChild();
      this.changeDetector.detectChanges();
      this.currentRowAsset = asset;
      this.showYoutubeForm = true;
      this.youtubeEditMode = true;
    }
  }
  //----------- Toggle Document

  toggleEditDocumentForm(asset){

    if(asset?.id != this.currentRowAsset?.id)
    {
      this.cancelForm(true);
      this.destroyFormsChild();
      this.changeDetector.detectChanges();
      this.currentRowAsset = asset;
      this.showDocumentForm = true;
      this.docsEditMode = true;
    }
  }
  //----------- Toggle External

  toggleEditExternalForm(asset){

    if(asset?.id != this.currentRowAsset?.id)
    {
      this.cancelForm(true);
      this.destroyFormsChild();
      this.changeDetector.detectChanges();
      this.currentRowAsset = asset;
      this.showExternalForm = true;
      this.externalEditMode = true;
    }
  }

  //----------- Toggle Downloadable

  toggleEditDownloadableForm(asset){

    if(asset?.id != this.currentRowAsset?.id)
    {
      this.cancelForm(true);
      this.destroyFormsChild();
      this.changeDetector.detectChanges();
      this.currentRowAsset = asset;
      this.showDownloadableForm = true;
      this.downloadableEditMode = true;
    }
  }

  
 /**
   * Get Assets from DB
   *
   */

  private _getAssets() {
    this.chapterService.getAssetsByLectureId(this.lecture.id).subscribe(assets => {
      this.setAssetDefaultData(assets);
     })  
  }

  /**
  * Defaul requirement data
  */

  setAssetDefaultData(assets) {
    this.deleteAll();
    for (let index = 0; index < assets.length; index++) {
      this.addAsset(assets[index]);
    }
  }

 /**
  * Delete all assets to reset
  */

  deleteAll() {
    while (this.courseAssetsForm.get('assets')['controls'].length !== 0) {
      (<FormArray>this.courseAssetsForm.controls['assets']).removeAt(0);
    }
  }
  /**
  * Add new asset item from db to fromArray
  */

  addAsset(asset: Asset) {
    let assets = this.courseAssetsForm.get('assets') as FormArray;
    assets.push(this.createAssetsArray(asset));
  }

  /**
  * Create new Asset item from db to formArray
  */

  createAssetsArray(asset: Asset) {
    return this.formBuilder.group({
      id: asset._id,
      lecture:asset.lecture,
      description: [asset.description, [Validators.required]],
      section:asset.section,
      fileType:asset.fileType,
      
      uploadedFile:asset.uploadedFile,
      uploadedFileName:asset.uploadedFileName,
      encodedFileName:asset.encodedFileName,
      uploadedFileSize:asset.uploadedFileSize,
      uploadedFileType:asset.uploadedFileType,
     
      pageCount:asset.pageCount,

      duration:asset.duration,
      hours:asset.hours,
      minutes:asset.minutes,
      seconds:asset.seconds,

      isExternal:asset.isExternal,
      isPublished:asset.isPublished
    });
  }


 
  /**
  * Warning message to delete asset (any Asset)
  *
  */

  deleteAsset(AssetId: string) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-danger ml-1'
      }
    }).then((result) => {
      if (result.value) {
        this._deleteAsset(AssetId);

        Swal.fire({
          title: 'Deleted!',
          text: 'Content has been deleted.',
          icon: 'success',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        });
      }
      else {
        Swal.fire({
          title: 'Cancelled',
          text: 'Content has not been deleted',
          icon: 'error',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
       }
      }
    );
  }

  /**
  * Delete Asset from DB
  *
  */

  private _deleteAsset(assetId) {
    this._assetSerivce.deleteAsset(assetId).subscribe(
      () => {  
        this._getAssets();
       },

      () => {
        this.toastr.error('Content is not deleted!', 'Error!', {
          toastClass: 'toast ngx-toastr',
          closeButton: true
        });
        return false;
      }
    );
  }


    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this.onDestroy.next();
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
      this.subscription.unsubscribe();
    }
  
}
