import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Input, Component, OnInit, ViewChild, SimpleChanges, EventEmitter, Output, ElementRef } from '@angular/core';
import {FormGroup, NgForm } from '@angular/forms';

import { AssetService } from 'app/services/asset.service';
import { FileUploader } from 'ng2-file-upload';
import { Subject } from 'rxjs';


import { ToastrService } from 'ngx-toastr';
import { Lecture } from 'app/models/lecture';



@Component({
  selector: 'asset-downloadable-form',
  templateUrl: './asset-downloadable-form.component.html'
})
export class AssetDownloadableFormComponent implements OnInit {
  @ViewChild('fileInputVariable') fileInputVariable: ElementRef;
  @ViewChild('assetEditDownloadableForm') assetEditDownloadableForm: NgForm;

  public currentRow: any;
  public error:string;

  public uploadedFile:string| ArrayBuffer;
  isSubmitted = false;
  form:FormGroup;
  currentAssetId:string;

  progress: number = 0;

  public editMode=false;

  public inputUpdated=false;
  public loading:boolean = false;


  // Input Decorator
   @Input() lecture: Lecture;
   @Input() asset: any;

   @Output() isUpdatedSuccess = new EventEmitter<boolean>();
   @Output() isCancelNewDownloadable = new EventEmitter<boolean>();
   @Output() isInputUpdated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;

  public uploader: FileUploader = new FileUploader({
    isHTML5: true,
    queueLimit:2,
    disableMultipart:true,
    maxFileSize:100 * 1024 * 1024,
    allowedMimeType: 
    [
      "application/zip",
    "application/x-rar-compressed", ]


  
  });
  /**
   * Constructor
   *
   */
  constructor(
    private _assetSerivce:AssetService,
    private toastr: ToastrService
    ) { 
      this._unsubscribeAll = new Subject();
    }
  /**
   * On Initial
   *
   */

    ngOnInit(): void {

      this.isUpdatedSuccess.emit(false);
      this.isCancelNewDownloadable.emit(false);
      this.isInputUpdated.emit(false);
    
      this._initForm();
     if(this.asset)
     {
      this.editMode = true;
      this.currentRow = this.asset;
      this.currentAssetId=this.asset.id;
      this._setFormValues(this.asset);
     }
    }
// ----- /End initiate

inputChange(event:any){
this.isInputUpdated.emit(true);
this.inputUpdated = true;
}

countChange(value) {
  this.isInputUpdated.emit(true);
  this.inputUpdated = true;
}

   /*
   Cancel any updates
   */ 
    cancelNewDownloadable(){
      this.deleteUploadedFile();
      this.isCancelNewDownloadable.emit(true);
    }

    /* 
    Scroll to form
    */

    ngAfterViewInit() {
      window.scrollTo(1900, 540);
    }

    /* 
    Reset Uploaded file before uploading process
    */
  

  deleteUploadedFile(){
    this.fileInputVariable.nativeElement.value = "";
    this.uploader.destroy();
    this.uploader.clearQueue();
   }


    /* 
    Reset Form With Default Values
    */
   resetFormWithDefaultValues() {
    this.isInputUpdated.emit(false);
    this.inputUpdated = false;

    this.assetEditDownloadableForm.resetForm(this.currentRow);  
    this.deleteUploadedFile();
  }

      /* 
    Initiate form
    */

      private _initForm(){
        let section = 'lecture_explenation';
        let fileType = 'downloadable';
        this.form = this._assetSerivce._initForm(this.currentAssetId,this.lecture,section,fileType);
      }   

  /**
   * set value if this in update mode
   * 
   * @param asset 
   */    
   
   private _setFormValues(asset:any){
        
        this.assetDownloadableForm.description.setValue(asset.description);

        this.assetDownloadableForm.uploadedFile.setValue(asset.uploadedFile);
        this.assetDownloadableForm.uploadedFileName.setValue(asset.uploadedFileName);
        this.assetDownloadableForm.encodedFileName.setValue(asset.encodedFileName);
        this.assetDownloadableForm.uploadedFileSize.setValue(asset.uploadedFileSize);
        this.assetDownloadableForm.uploadedFileType.setValue(asset.uploadedFileType);


        this.assetDownloadableForm.isExternal.setValue(asset.isExternal);
        this.assetDownloadableForm.isPublished.setValue(asset.isPublished);
        this.assetDownloadableForm.uploadedFile.setValidators([]);
        this.assetDownloadableForm.uploadedFile.updateValueAndValidity();
      }


  /**
   * On Submit
   *
   */
   assetDownloadableFormSubmit() {

    this.isUpdatedSuccess.emit(false);
    this.isSubmitted = true;

    if (this.form.invalid) return;

    const assetDownloadableFormData = new FormData();
    Object.keys(this.assetDownloadableForm).map((key)=>{
      assetDownloadableFormData.append(key,this.assetDownloadableForm[key].value);
     });
    
    this._updateAsset(assetDownloadableFormData);
  }


  _updateAsset(assetDownloadableFormData: FormData) {

   this._assetSerivce.updateAsset(
      assetDownloadableFormData,
      this.currentAssetId
    )
    .subscribe((event: HttpEvent<any>) => {

      switch (event.type) {
   
        case HttpEventType.Sent:
          break;
        case HttpEventType.ResponseHeader:
          break;
        case HttpEventType.UploadProgress:
          this.progress = Math.round(event.loaded / event.total * 100);
          this.loading = true;

          break;
        case HttpEventType.Response:

          this.currentRow = event.body;
          this.asset = event.body;
          this.loading = false;

          this.isUpdatedSuccess.emit(true);
          this.isInputUpdated.emit(false);
          
          this.toastr.success(this.editMode?'Content updated successfuly!':'Content created successfuly!', 'Success!', {
            toastClass: 'toast ngx-toastr',
            closeButton: true
          });
      setTimeout(() => {
            this.progress = 0;
          }, 1500);
          this.deleteUploadedFile();
      }
    },  (err) => {
      this.toastr.error(this.editMode?'Content wasn\'t updated!':'Content wasn\'t created!', 'Error!', {
        toastClass: 'toast ngx-toastr',
        closeButton: true
      });
    this.setErrorMessage(this.editMode?'Update failed, something went wrong':'Create document failed, something went wrong');
    this.loading = false; 
    });
  }



  uploadDownloadable(event:any){

    if (this.uploader.queue.length>1) {
      this.uploader.queue[0].remove();
    }
    const file = event.target.files[0];

    this.uploader.onWhenAddingFileFailed = (file) => {
    this.error = "File type is not supported";
    this.deleteUploadedFile();
    }

    if (file) {
  
      let uploadedFileName =this.uploader.queue[0]?.file?.name;
      let uploadedFileSize = (this.uploader.queue[0]?.file?.size / 1024 / 1024).toFixed(2)+' MB';
      let uploadedFileType =this.uploader.queue[0]?.file?.type;
  
      this.form.patchValue({uploadedFile:file});
      this.form.patchValue({uploadedFileName:uploadedFileName});
      this.form.patchValue({uploadedFileSize:uploadedFileSize});
      this.form.patchValue({uploadedFileType:uploadedFileType});
     
      this.form.get('uploadedFile').updateValueAndValidity();
      const fileReader = new FileReader();
      fileReader.onload = () =>{
        this.uploadedFile = fileReader.result
      };
      fileReader.readAsDataURL(file);
    }
    }
 

 
  get assetDownloadableForm(){
    return this.form.controls;
  }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
    }

    setErrorMessage(message: string, timeOut = 3000) {
      this.error = message;
    this.ngAfterViewInit();
      setTimeout(() => {
        this.error = null;
      }, timeOut);
    }
  
  
}
