import { Component, OnInit, OnDestroy, ViewEncapsulation, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CategoryService } from 'app/services/category.service';
import { ChapterService } from 'app/services/chapter.service';
import { CourseService } from 'app/services/course.service';

import Stepper from 'bs-stepper';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InstructorDashboardService } from '../instructor-dashboard.service';

@Component({
  selector: 'instructor-course-form',
  templateUrl: './instructor-course-form.component.html',
  styleUrls: ['./instructor-course-form.scss'],
  encapsulation: ViewEncapsulation.None
})
export class InstructorCourseFormComponent implements OnInit, OnDestroy {
  // public
  public data: any;
  public instructor:any;
  public contentHeader: object;

  public coreConfig: any;
  public isRTL:boolean;
  public appLanguage:string;
  public appLanguage1:string;
  public currentCourseId:string;
  public course:any;
public courseData:FormData;

  public tempCategories = [];
  public categories = [];

  public chapters = [];

  editmode=false
 
  // private


  private _unsubscribeAll: Subject<any>;
  private courseWizardStepper: Stepper;
 
  /**
   * Constructor
   *
   * @param {InstructorDashboardService} _insturctorService
   */
  constructor(
    private _insturctorService: InstructorDashboardService,
    private _CategoryService: CategoryService,
    private _chapterService:ChapterService,
    private _courseSerivce:CourseService,
    private router:Router,
    private route:ActivatedRoute
     ) {
    this._unsubscribeAll = new Subject();

  }

 
  // Lifecycle Hooks
  // -----------------------------------------------------------------------------------------------------

  /**
   * On init
   */
  ngOnInit(): void {
    this.courseWizardStepper = new Stepper(document.querySelector('#stepper1'), {});

    this.coreConfig = JSON.parse(localStorage.getItem('config'));
    this.isRTL = this.coreConfig.app.appLanguage=='ar' &&  this.coreConfig.layout.type=='horizontal';
 this.appLanguage = this.coreConfig.app.appLanguage;

       this._insturctorService.onSettingsChanged.pipe(takeUntil(this._unsubscribeAll)).subscribe(response => {
        this.data = response;
    
       });
       
     this._getInstructorInfo(this.data._id);
     this._getCategories();
     this._getCourseDetails();
     this._getChapters();
      }

      updateChapters(isUpdated:boolean){
        if (isUpdated) {
       this._getChapters();
        }
      }


      getCourseFormData(courseData:FormData){
       this.courseData =courseData;
      }

      getNewCourse(newCourse:any){
        this.course =newCourse;
       }    

      private _getCourseDetails(){
      
        this.route.params.subscribe(params => {
          if(params.id){
            this.editmode = true;
            this.currentCourseId = params.id;
            this._courseSerivce.getCourse(params.id).subscribe(course=>{
              this.course = course;
            })
          }
        })
        }
  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next();
    this._unsubscribeAll.complete();
  }

  checNewCourse(isCourseCreated:boolean){
    if (isCourseCreated) {
      this.router.navigateByUrl(`instructor/create-course/${this.course._id}`);
    }
  }


  private _getCategories(){
    this._CategoryService.getCategories().pipe(takeUntil(this._unsubscribeAll))
    .subscribe(response => {
      
      this.tempCategories = response;

          this.tempCategories.forEach(elements => {
              elements.translate.forEach(translate=>{
                if(translate.lang==this.appLanguage){
                this.categories.push
                ({ title: translate.title,
                   id:elements.id})
                }
                });
            });
           
      
  });

  }
/**
 * get chapters
 */

  private _getChapters(){
    this.route.params.subscribe(params => {
      if(params.id){ 
        this._chapterService.getChapters(params.id).pipe(takeUntil(this._unsubscribeAll))
        .subscribe(chapters => {
         this.chapters = chapters;
           });
      }
    })
  }

   /**
   * Get instructor course info
   */ 
    private _getInstructorInfo(instructorId:string){
      this._insturctorService.getInstructorInfo(instructorId).pipe(takeUntil(this._unsubscribeAll))
      .subscribe(response => {
        this.instructor = response;
      });
    }

    getCourseId(courseId:string){
   this.currentCourseId = courseId;
     }



}
