import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Input, Component, OnInit, ViewChild, SimpleChanges, EventEmitter, Output, ElementRef } from '@angular/core';
import {FormGroup, NgForm } from '@angular/forms';

import { AssetService } from 'app/services/asset.service';
import { FileUploader } from 'ng2-file-upload';
import { Subject } from 'rxjs';

import { CommonService } from 'app/services/common.service';
import { ToastrService } from 'ngx-toastr';
import { Lecture } from 'app/models/lecture';




@Component({
  selector: 'asset-video-form',
  templateUrl: './asset-video-form.component.html'
})
export class AssetVideoFormComponent implements OnInit {
  @ViewChild('fileInputVariable') fileInputVariable: ElementRef;
  @ViewChild('assetEditVideoForm') assetEditVideoForm: NgForm;

  public currentRow: any;
  public error:string;
  public uploadedFile:string| ArrayBuffer;
  isSubmitted = false;
  form:FormGroup;
  currentAssetId:string;

  progress: number = 0;

  public editMode=false;

  public inputUpdated=false;
  public loading:boolean = false;


  // Input Decorator
   @Input() lecture: Lecture;
   @Input() asset: any;
   @Output() isUpdatedSuccess = new EventEmitter<boolean>();
   @Output() isCancelNewVideo = new EventEmitter<boolean>();
   @Output() isInputUpdated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;

  public uploader: FileUploader = new FileUploader({
    isHTML5: true,
    queueLimit:2,
    disableMultipart:true,
    maxFileSize:100 * 1024 * 1024,
    allowedMimeType: 
    [  "video/3gpp",
      'video/mp4',
      "video/mpeg",
      "video/x-msvideo"]
  ,
  });
  /**
   * Constructor
   *
   */
  constructor(
    private _assetSerivce:AssetService,
    private _commonService:CommonService,
    private toastr: ToastrService

    ) { 
      this._unsubscribeAll = new Subject();
 
    }
  /**
   * On Initial
   *
   */

    ngOnInit(): void {
      //this.error = 'Invalid Video! video is less than 1 second';
      this.isUpdatedSuccess.emit(false);
      this.isCancelNewVideo.emit(false);
      this.isInputUpdated.emit(false);
    
      this._initForm();
     if(this.asset)
     {
      this.editMode = true;
      this.currentRow = this.asset;
      this.currentAssetId=this.asset.id;
      this._setFormValues(this.asset);
     }
    }
// ----- /End initiate

inputChange(event:any){
this.isInputUpdated.emit(true);
this.inputUpdated = true;
}

   /*
   Cancel any updates
   */ 
    cancelNewVideo(){
      this.deleteUploadedFile();
      this.isCancelNewVideo.emit(true);
    }

    /* 
    Scroll to form
    */

    ngAfterViewInit() {
      window.scrollTo(1900, 540);
    }

    /* 
    Reset Uploaded file before uploading process
    */
  

  deleteUploadedFile(){
    this.fileInputVariable.nativeElement.value = "";
    this.uploader.destroy();
    this.uploader.clearQueue();
   }


    /* 
    Reset Form With Default Values
    */
   resetFormWithDefaultValues() {
    this.isInputUpdated.emit(false);
    this.inputUpdated = false;
    this.assetEditVideoForm.resetForm(this.currentRow);  
    this.deleteUploadedFile();
  }

      /* 
    Initiate form
    */

      private _initForm(){
        let section = 'lecture_explenation';
        let fileType = 'video';

        this.form = this._assetSerivce._initForm(this.currentAssetId,this.lecture,section,fileType);
      }   

  /**
   * set value if this in update mode
   * 
   * @param asset 
   */    
   
   private _setFormValues(asset:any){
        
        this.assetVideoForm.description.setValue(asset.description);

        this.assetVideoForm.uploadedFile.setValue(asset.uploadedFile);
        this.assetVideoForm.uploadedFileName.setValue(asset.uploadedFileName);
        this.assetVideoForm.encodedFileName.setValue(asset.encodedFileName);
        this.assetVideoForm.uploadedFileSize.setValue(asset.uploadedFileSize);
        this.assetVideoForm.uploadedFileType.setValue(asset.uploadedFileType);

        this.assetVideoForm.duration.setValue(asset.duration);
        
        this.assetVideoForm.hours.setValue(asset.hours);
        this.assetVideoForm.minutes.setValue(asset.minutes);
        this.assetVideoForm.seconds.setValue(asset.seconds);

        this.assetVideoForm.isExternal.setValue(asset.isExternal);
        this.assetVideoForm.isPublished.setValue(asset.isPublished);
        this.assetVideoForm.uploadedFile.setValidators([]);
        this.assetVideoForm.uploadedFile.updateValueAndValidity();
      }


  /**
   * On Submit
   *
   */
   assetVideoFormSubmit() {

    this.isUpdatedSuccess.emit(false);
    this.isSubmitted = true;

    if (this.form.invalid) return;

    const assetVideoFormData = new FormData();
    Object.keys(this.assetVideoForm).map((key)=>{
      assetVideoFormData.append(key,this.assetVideoForm[key].value);
     });
    
    this._updateAsset(assetVideoFormData);
  }


  _updateAsset(assetVideoFormData: FormData) {

   this._assetSerivce.updateAsset(
      assetVideoFormData,
      this.currentAssetId
    )
    .subscribe((event: HttpEvent<any>) => {

      switch (event.type) {
   
        case HttpEventType.Sent:
          break;
        case HttpEventType.ResponseHeader:
          break;
        case HttpEventType.UploadProgress:
          this.progress = Math.round(event.loaded / event.total * 100);
          this.loading = true;

          break;
        case HttpEventType.Response:

          this.currentRow = event.body;
          this.asset = event.body;
          this.loading = false;

          this.isUpdatedSuccess.emit(true);
          this.isInputUpdated.emit(false);
          this.toastr.success(this.editMode?'Content updated successfuly!':'Content created successfuly!', 'Success!', {
            toastClass: 'toast ngx-toastr',
            closeButton: true
          });
      setTimeout(() => {
            this.progress = 0;
          }, 1500);
          this.deleteUploadedFile();
      }
    },  (err) => {
      this.toastr.error(this.editMode?'Content wasn\'t updated!':'Content wasn\'t created!', 'Error!', {
        toastClass: 'toast ngx-toastr',
        closeButton: true
      });
    this.setErrorMessage(this.editMode?'Update failed, something went wrong':'Create video failed, something went wrong');
    this.loading = false; 
    });
  }


  setFileInfo(file) {
    var video = document.createElement('video');
    video.preload = 'metadata';
    video.onloadedmetadata = ()=> {
      window.URL.revokeObjectURL(video.src);
      var duration = video.duration;
      file.duration = duration;
      this.form.patchValue({duration:file.duration});

      let durationTime = this._commonService.getTimes(file.duration);
      this.form.patchValue({hours:durationTime[0]});
      this.form.patchValue({minutes:durationTime[1]});
      this.form.patchValue({seconds:durationTime[2]});
    }
    video.src = URL.createObjectURL(file);
  }


  uploadVideo(event:any){
    if (this.uploader.queue.length>1) {
      this.uploader.queue[0].remove();
    }
    const file = event.target.files[0];
    this.uploader.onWhenAddingFileFailed = (file) => {
    this.error = "File type is not supported";
      this.deleteUploadedFile();
    }

    if (file) {
     this.setFileInfo(file);
      let uploadedFileName =this.uploader.queue[0]?.file?.name;
      let uploadedFileSize = (this.uploader.queue[0]?.file?.size / 1024 / 1024).toFixed(2)+' MB';
      let uploadedFileType =this.uploader.queue[0]?.file?.type;
  
      this.form.patchValue({uploadedFile:file});
      this.form.patchValue({uploadedFileName:uploadedFileName});
      this.form.patchValue({uploadedFileSize:uploadedFileSize});
      this.form.patchValue({uploadedFileType:uploadedFileType});
     
      this.form.get('uploadedFile').updateValueAndValidity();
      const fileReader = new FileReader();
      fileReader.onload = () =>{
        this.uploadedFile = fileReader.result
      };
      fileReader.readAsDataURL(file);
    }
    }
 

 
  get assetVideoForm(){
    return this.form.controls;
  }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
    }

    setErrorMessage(message: string, timeOut = 3000) {
      this.error = message;
    this.ngAfterViewInit();
      setTimeout(() => {
        this.error = null;
      }, timeOut);
    }
  
  
}
