import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Input, Component, OnInit, ViewChild, EventEmitter, Output, ElementRef } from '@angular/core';
import {FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';

import { AssetService } from 'app/services/asset.service';
import { Subject } from 'rxjs';
import * as moment from 'moment';

import { CommonService } from 'app/services/common.service';
import { ToastrService } from 'ngx-toastr';
import { Timestamp } from 'rxjs/internal/operators/timestamp';
import { Lecture } from 'app/models/lecture';

//AIzaSyCUGTLCPqwF0kLzy81zT6WdNnwQL8ArfMk


@Component({
  selector: 'asset-youtube-form',
  templateUrl: './asset-youtube-form.component.html'
})
export class AssetYoutubeFormComponent implements OnInit {

  @ViewChild('fileInputVariable') fileInputVariable: ElementRef;
  @ViewChild('assetEditYoutubeForm') assetEditYoutubeForm: NgForm;

  public currentRow: any;
  public error:string;

  public hours:string;
  public minutes:string;
  public seconds:string;

  public uploadedFile:string| ArrayBuffer;
  isSubmitted = false;
  form:FormGroup;
  currentAssetId:string;

  progress: number = 0;

  public editMode = false;

  public inputUpdated = false;
  public loading:boolean = false;



  // Input Decorator
   @Input() lecture: Lecture;
   @Input() asset: any;
   @Output() isUpdatedSuccess = new EventEmitter<boolean>();
   @Output() isCancelNewYoutube = new EventEmitter<boolean>();
   @Output() isInputUpdated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;


  /**
   * Constructor
   *
   */
  constructor(
    private _assetSerivce:AssetService,
    private toastr: ToastrService,
    private formBuilder:FormBuilder,
    private _commonService:CommonService,
    ) { 
      this._unsubscribeAll = new Subject();
    }
  /**
   * On Initial
   *
   */

    ngOnInit(): void {
      //this.error = 'Invalid Video! audio is less than 1 second';
      this.isUpdatedSuccess.emit(false);
      this.isCancelNewYoutube.emit(false);
      this.isInputUpdated.emit(false);
    
      this._initForm();
     if(this.asset)
     {
      this.editMode = true;
      this.currentRow = this.asset;
      this.currentAssetId=this.asset.id;
      this._setFormValues(this.asset);
     }

    }
// ----- /End initiate

 _getYoutubeInfo(event) {
  this.loading = true;
this.assetYoutubeForm.description.setValue('');

  const youtubeUrl = event.target.value;

  const regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;


  const match = youtubeUrl.match(regExp);
  let ytVideoId =  (match&&match[7].length==11)? match[7] : youtubeUrl;
  this.form.patchValue({uploadedFile:ytVideoId});

  // Get Youtube Title

  this._assetSerivce.getYoutubeTitle(ytVideoId).subscribe(ytVideoInfo => {
    if (ytVideoInfo?.items[0]) {

      this.loading = false;
      this.assetYoutubeForm.description.setValue(ytVideoInfo.items[0].snippet.title);
      this.error="";
    }else{
    this.loading = true;
    this.error="Youtube link is not valid";
    this.assetYoutubeForm.description.setValue('');}
   })  

// Get Youtube Duration   
   this._assetSerivce.getYoutubeDuration(ytVideoId).subscribe(ytVideoInfo => {

    if (ytVideoInfo?.items[0]) {
      const isoTime = ytVideoInfo.items[0].contentDetails.duration;
      this.loading = false;

     let millis = moment.duration(isoTime).asMilliseconds();
     var duration = ((millis / 1000)-1).toFixed(0);

     this.form.patchValue({duration:duration});

   

     let durationTime = this._commonService.getTimes(duration);

     this.hours = durationTime[0].toString();
     this.minutes = durationTime[1].toString();
     this.seconds = durationTime[2].toString();

     this.form.patchValue({hours:durationTime[0]});
     this.form.patchValue({minutes:durationTime[1]});
     this.form.patchValue({seconds:durationTime[2]});


     this.error="";
    }else{
      this.error="Youtube link is not valid";
    this.loading = true;
    this.assetYoutubeForm.description.setValue('');
  
  }
   })  

}



 getTitle(snippet_json_data){
  var title = snippet_json_data.title;
  return title;
}



inputChange(event:any){
this.isInputUpdated.emit(true);
this.inputUpdated = true;
}

   /*
   Cancel any updates
   */ 
    cancelNewYoutube(){
      this.isCancelNewYoutube.emit(true);
    }

  //only paste  
     onlyPasteInput(event: KeyboardEvent) {
  
      //if is pressed a different key than c or v, or the ctrl key isn't pressed stop event
      if (!event.metaKey && !event.ctrlKey || (event.key.toLowerCase() !== 'c' && event.key.toLowerCase() !== 'v')) {
        event.stopImmediatePropagation();
        event.stopPropagation();
        event.preventDefault();
      } 
    
      // else execute the default event behaviour
    }
    /* 
    Scroll to form
    */

    ngAfterViewInit() {
      window.scrollTo(1900, 540);
    }

 
    /* 
    Reset Form With Default Values
    */
   resetFormWithDefaultValues() {
    this.isInputUpdated.emit(false);
    this.inputUpdated = false;
    this.assetEditYoutubeForm.resetForm(this.currentRow);
    this.hours = this.currentRow.hours;  
    this.minutes = this.currentRow.minutes;  
    this.seconds = this.currentRow.seconds;  
  }

      /* 
    Initiate form
    */
private _initForm(){
  this.form=this.formBuilder.group({
    lecture:this.lecture.id,
    description: ['', Validators.required],
    section: 'lecture_explenation',
    fileType:'youtube',

    uploadedFile:['', Validators.required],
    uploadedFileName:'',
    uploadedFileSize:'',
    uploadedFileType:'',
    encodedFileName:'',

    duration:'',
    hours:'',
    minutes:'',
    seconds:'',
    
    isPublished:true,
    isExternal:false,
  })

      }   

  /**
   * set value if this in update mode
   * 
   * @param asset 
   */    
   
   private _setFormValues(asset:any){
        
        this.assetYoutubeForm.description.setValue(asset.description);

        this.assetYoutubeForm.uploadedFile.setValue(asset.uploadedFile);
        this.assetYoutubeForm.uploadedFileName.setValue(asset.uploadedFileName);
        this.assetYoutubeForm.encodedFileName.setValue(asset.encodedFileName);
        this.assetYoutubeForm.uploadedFileSize.setValue(asset.uploadedFileSize);
        this.assetYoutubeForm.uploadedFileType.setValue(asset.uploadedFileType);

        this.assetYoutubeForm.duration.setValue(asset.duration);
       
        this.hours = asset.hours;
        this.minutes = asset.minutes;
        this.seconds = asset.seconds;

        this.assetYoutubeForm.isExternal.setValue(asset.isExternal);
        this.assetYoutubeForm.isPublished.setValue(asset.isPublished);

      }


  /**
   * On Submit
   *
   */
   assetYoutubeFormSubmit() {

    this.isUpdatedSuccess.emit(false);
    this.isSubmitted = true;

    if (this.form.invalid) return;

    const assetYoutubeFormData = new FormData();
    Object.keys(this.assetYoutubeForm).map((key)=>{
      assetYoutubeFormData.append(key,this.assetYoutubeForm[key].value);
     });
    
    this._updateAsset(assetYoutubeFormData);
  }


  _updateAsset(assetYoutubeFormData: FormData) {

   this._assetSerivce.updateAsset(
      assetYoutubeFormData,
      this.currentAssetId
    )
    .subscribe((event: HttpEvent<any>) => {

      switch (event.type) {
   
        case HttpEventType.Sent:
          break;

        case HttpEventType.ResponseHeader:
          break;
  
        case HttpEventType.Response:

          this.currentRow = event.body;
          this.asset = event.body;
          this.loading = false;

          this.isUpdatedSuccess.emit(true);
          this.isInputUpdated.emit(false);
          
          this.toastr.success(this.editMode?'Content updated successfuly!':'Content created successfuly!', 'Success!', {
            toastClass: 'toast ngx-toastr',
            closeButton: true
          });
      setTimeout(() => {
            this.progress = 0;
          }, 1500);
      }
    },  (err) => {
      this.toastr.error(this.editMode?'Content wasn\'t updated!':'Content wasn\'t created!', 'Error!', {
        toastClass: 'toast ngx-toastr',
        closeButton: true
      });
    this.setErrorMessage(this.editMode?'Update failed, something went wrong':'Create audio failed, something went wrong');
    this.loading = false; 
    });
  }


 
  get assetYoutubeForm(){
    return this.form.controls;
  }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
    }

    setErrorMessage(message: string, timeOut = 3000) {
      this.error = message;
    this.ngAfterViewInit();
      setTimeout(() => {
        this.error = null;
      }, timeOut);
    }
  
  
}
