import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Asset } from 'app/models/asset';
import { Lecture } from 'app/models/lecture';
import { ChapterService } from 'app/services/chapter.service';

import { DragulaService } from 'ng2-dragula';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import Swal from 'sweetalert2';


@Component({
  selector: 'instructor-course-lectures',
  templateUrl: './instructor-course-lectures.component.html'
})

export class InstructorcourseLecturesComponent implements OnInit, OnDestroy {

  subscription = new Subscription();
  // public
  public error: string;
  current: any;

  editLectureMode: boolean = false;
  courseLecturesForm: FormGroup;

  @Input() appLanguage: string;
  @Input() public chapter: any;
  @Output() chapterUpdated = new EventEmitter<boolean>();



  // private
  private _unsubscribeAll: Subject<any>;
  // private


  constructor(
    private dragulaService: DragulaService,
    private formBuilder: FormBuilder,
    private chapterService: ChapterService,
    private toastr: ToastrService
  ) {
    this._unsubscribeAll = new Subject();

  }

  /**
 * On init --------------------------------------------------------
 */

  ngOnInit(): void {
    this.chapterUpdated.emit(false);
    this.dragulaService.createGroup(`lectures-list${this.chapter.sortNumber}`, {
      moves: function (el, container, handle) {
        return (handle.classList.contains('handle-lecture') && 
        !handle.classList.contains('edit-lecture-mode'));
      }
    });
    this.dragulaService.out(`lectures-list${this.chapter.sortNumber}`).subscribe((value) => {
      this.createCourseLectures();
    });

    this.courseLecturesForm = this.formBuilder.group({
      lectures: this.formBuilder.array([]),
    })
    this._getLectures();

    

  }



  //------------------------ Chapters ------------------------

  /**
   * Get Lectures from DB
   *
   */
  private _getLectures() {
      this.chapterService.getLectures(this.chapter.id).subscribe(lectures => {
      this.editLectureMode=false;
     
      this.setLectureDefaultData(lectures);
     })

      
  }

  /**
  * Defaul requirement data
  */

  setLectureDefaultData(lectures) {
   
    this.deleteAll();
    for (let index = 0; index < lectures.length; index++) {
      this.addLecture(lectures[index]);
    }
  }

 /**
  * Delete all chapters to reset
  */

  deleteAll() {
    while (this.courseLecturesForm.get('lectures')['controls'].length !== 0) {
      (<FormArray>this.courseLecturesForm.controls['lectures']).removeAt(0);
    }
  }
  /**
  * Add new lecture item from db to fromArray
  */

  addLecture(lecture: Lecture) {
    let lectures = this.courseLecturesForm.get('lectures') as FormArray;
    lectures.push(this.createLecturesArray(lecture));
  }

  /**
  * Create new lecture item from db to formArray
  */

  createLecturesArray(lecture: Lecture) {
    return this.formBuilder.group({
      id: lecture._id,
      sortNumber: lecture.sortNumber,
      chapter:lecture.chapter,
      title: [lecture.title, [Validators.required]],
      shown:false,
      assets: (<any>lecture.assets).length > 0 ? new FormArray([this.createAssetsArray(lecture.assets)]) : this.formBuilder.array([])

    });
  }
  /**
  * Add Lecture item from db to fromArray
  */







  createAssetsArray(asset: Asset) {
    {
      return this.formBuilder.group({
        description: asset.description,
        lecture: asset.lecture,
        section:asset.section,
        fileType:asset.fileType,
       

        uploadedFile:asset.uploadedFile,
        uploadedFileName:asset.uploadedFileName,
        encodedFileName:asset.encodedFileName,
        uploadedFileSize:asset.uploadedFileSize,
        uploadedFileType:asset.uploadedFileType,
       

       pageCount:asset.pageCount,

       duration:asset.duration,
       hours:asset.hours,
       minutes:asset.minutes,
       seconds:asset.seconds,
       

       isExternal:asset.isExternal,
       isPublished:asset.isPublished
      })
    }
  }



  /**
  * Create new empty chapter
  *
  */
   createEmptyLecturesArray() {
    this.editLectureMode=true;
    let lectures = this.courseLecturesForm.get('lectures') as FormArray;
    lectures.push(
      this.formBuilder.group({
        id: null,
        title: ['', [Validators.required]],
        chapter: this.chapter.id,
        assets: [],
        shown:false,
      })
    );
  }

  /**
  * Delete chapter and all its lectures
  *
  * @param index
  */
  deleteLectureItem(index: any) {
   this.checkDragValidation();
    const items = this.courseLecturesForm.get('lectures')['controls'];
    const lectures = this.courseLecturesForm.value.lectures;
    for (let i = 0; i < items.length; i++) {
   
      if (items.indexOf(items[i]) === index) {
        if (lectures[index].id) {
          this.chapterService.getAssetsByLectureId(lectures[index].id).subscribe(assets=>{
            if (assets.length > 0) {
              this.setErrorMessage('All content must be deleted before the lecture is deleted', 8000);
            }else{
              this.deleteLecture(lectures[index].id, i); 
            }
          })
        }
        else //New item which is not stored in DB
        {
          (<FormArray>this.courseLecturesForm.controls['lectures']).removeAt(i);
          this.checkDragValidation();
        }
      }
    }
  }


  /**
  * Warning message to delete chapter
  *
  */

  deleteLecture(lectureId: string, index) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-danger ml-1'
      }
    }).then((result) => {
      if (result.value) {
        this._deleteLecture(lectureId);
        (<FormArray>this.courseLecturesForm.controls['lectures']).removeAt(index);
        this.updateLectureSorting();
        Swal.fire({
          title: 'Deleted!',
          text: 'Lecture has been deleted.',
          icon: 'success',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        });
      }
      else {
        Swal.fire({
          title: 'Cancelled',
          text: 'Lecture has not been deleted',
          icon: 'error',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
       }
      }
    );
  }

  /**
  * Delete lectureId from DB
  *
  */

  private _deleteLecture(lectureId) {
    this.chapterService.deleteLecture(lectureId).subscribe(
      () => {
            this._getLectures();
            this.chapterUpdated.emit(true);
       },
      () => {
        this.toastr.error('Lecture is not deleted!', 'Error!', {
          toastClass: 'toast ngx-toastr',
          closeButton: true
        });
        return false;
      }
    );
  }

  /**
  * Update lectures sorting after delete a chapter
  *
  */

  updateLectureSorting() {
    const lectures = this.courseLecturesForm.value.lectures;
    for (let index = 0; index < lectures.length; index++) {
      let lecture = this.courseLecturesForm.get('lectures')['controls'][index] as FormArray;
      lecture.value.sortNumber = index + 1;
      this.updateLecture(lecture.value);
    }
  }



  /**
  * Save new chapter to DB
  *
  */

  updateOrCreateLecture(lecture: any) {
    //const chapter = this.courseLecturesForm.value.chapters[index];
    const sortNumber = this.courseLecturesForm.value.lectures.length;
    if (lecture.id) {
      this.updateLecture(lecture);
    }
    else {
      lecture.sortNumber = sortNumber;
      this.createLecture(lecture);
    }
  }

  /**
  * Update chapter to DB
  *
  */

  updateLecture(lecture: Lecture) {
    this.chapterService.updateLecture(lecture).subscribe(
      () => {
        this._getLectures();
      }, error => {
        this.setErrorMessage(error.error.message, 4000);
      }
    );
  }

  /**
  * Create chapter to DB
  *
  */
  createLecture(lecture: Lecture) {
    this.chapterService.createLecture(lecture).subscribe(
      () => {
          this._getLectures();
          this.chapterUpdated.emit(true);
      }, error => {
        this.setErrorMessage(error.error.message, 4000);
      }
    );
  }

  /**
  * Error Message
  *
  */

  setErrorMessage(message: string, timeOut = 3000) {
    this.error = message;
    window.scroll({
      top: 70,
      left: 0,
      behavior: 'smooth'
    });
    setTimeout(() => {
      this.error = null;
    }, timeOut);
  }

  /**
  * Update all chapters after Re Ordering
  *
  */

  updateMultiLectures(lectures: any) {
 
    this.chapterService.updateMultiLectures(lectures).subscribe(
      () => {
        this._getLectures();
      }, error => {
        this.setErrorMessage(error.error.message, 4000);
      }
    );
  }

  /**
  * Save new chapter to DB
  *
  */

        checkDragValidation(){
          const lectures = this.courseLecturesForm.value.lectures;
          if (this.courseLecturesForm.invalid) {
            this.editLectureMode=true;
          }else{
            this.editLectureMode=false;
          }
          for (let index = 0; index < lectures.length; index++) {
            if (!lectures[index].id) {
              this.editLectureMode=true;
            }else{
              this.editLectureMode=false;
            }
          }

        }

  createCourseLectures() {
    const lectures = this.courseLecturesForm.value.lectures;
    if (this.courseLecturesForm.invalid) {
      this.setErrorMessage('Please fill all the required field first!',5000);
     return;
    }

    this.courseLecturesForm.markAllAsTouched();
    for (let index = 0; index < lectures.length; index++) {
      lectures[index].sortNumber = index + 1; 
    }
    this.updateMultiLectures(lectures);
  }

  toggle(item, i) {
    item.shown = !item.shown;
    item.shown?this.editLectureMode=true:this.editLectureMode=false;  

    this.current = i;

  }

    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
      this.subscription.unsubscribe();
      this.dragulaService.destroy(`lectures-list${this.chapter.sortNumber}`);
    }
  
  
}
