import { Pipe, PipeTransform } from '@angular/core';
import { FormArray } from '@angular/forms';
import { Asset } from './asset';


@Pipe({
    name: 'assetfilter',
    pure: true
})

export class AssetFilterPipe implements PipeTransform {
  transform(items: FormArray[], filter: FormArray,countItem:{count:number}): FormArray[] {
    if (!items || !filter) {
      return items;
    }

    // filter items array, items which match and return true will be kept, false will be filtered out
   let item =  items.filter((item: FormArray) => this.applyFilter(item, filter));
   countItem.count = item.length;
   return item;
  }
  
  /**
   * Perform the filtering.
   * 
   * @param {Asset} asset The asset to compare to the filter.
   * @param {Asset} filter The filter to apply.
   * @return {boolean} True if asset satisfies filters, false if not.
   */
  applyFilter(asset: FormArray, filter: FormArray): boolean {
    

    for (let field in filter) {
      if (filter[field]) {
        if (typeof filter[field] === 'string') {
        
          if (asset[field].toLowerCase().indexOf(filter[field].toLowerCase()) === -1) {
            return false;
          }

           
          
        } else if (typeof filter[field] === 'number') {
          if (asset[field] !== filter[field]) {
            return false;
          }
        }
      }
    }

    return true;
  }
}