import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Input, Component, OnInit,OnDestroy, ViewChild, SimpleChanges, EventEmitter, Output, ElementRef, ChangeDetectorRef } from '@angular/core';
import {FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';

import { FileUploader } from 'ng2-file-upload';
import { Subject } from 'rxjs';


import { ToastrService } from 'ngx-toastr';
import { Assignment } from 'app/models/Assignment';
import { Lecture } from 'app/models/lecture';
import { AssignmentService } from 'app/services/assignment.service';



@Component({
  selector: 'lecture-assignment-form',
  templateUrl: './lecture-assignment-form.component.html'
})

export class LectureAssignmentFormComponent implements OnInit, OnDestroy {
  @ViewChild('fileInputVariable') fileInputVariable: ElementRef;
  @ViewChild('assignmentEditForm') assignmentEditForm: NgForm;

  private readonly onDestroy: Subject<any> = new Subject<any>();

  public error:string;
  public uploadedFile:string| ArrayBuffer;
  isSubmitted = false;
  form:FormGroup;
  currentAssignment:any;

  progress: number = 0;

  public editMode=false;
  public inputUpdated=false;
  public loading:boolean = false;


  // Input Decorator
   @Input() assignment: Assignment;
   @Input() lecture: Lecture;

   @Output() isUpdatedSuccess = new EventEmitter<boolean>();
   @Output() isAssignmentCanceled = new EventEmitter<boolean>();
   @Output() isInputUpdated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;
  // private


  public uploader: FileUploader = new FileUploader({
    isHTML5: true,
    queueLimit:2,
    disableMultipart:true,
    maxFileSize:100 * 1024 * 1024,
    allowedMimeType: 
    [
      "application/zip",
    "application/x-rar-compressed", ]
 });
  /**
   * Constructor
   *
   */
   constructor(
    private formBuilder:FormBuilder,
    private _assignmentService:AssignmentService,
    private toastr: ToastrService
    ) { 
      this._unsubscribeAll = new Subject();
    }
  /**
 * On init --------------------------------------------------------
 */

  ngOnInit(): void {
    console.log(this.lecture);
    this._initForm();

    this.isUpdatedSuccess.emit(false);
    this.isAssignmentCanceled.emit(false);
    this.isInputUpdated.emit(false);


    if(this.assignment)
    {
     this.editMode = true;
     this.currentAssignment = this.assignment;
    this._setFormValues(this.assignment);
    }
  }

  /**
   * set value if this in update mode
   * 
   * @param assignment 
   */    
   
   private _setFormValues(assignment:any){

    this.assignmentForm.description.setValue(assignment.description);
    this.assignmentForm.uploadedFile.setValue(assignment.uploadedFile);
    this.assignmentForm.uploadedFileName.setValue(assignment.uploadedFileName);
    this.assignmentForm.encodedFileName.setValue(assignment.encodedFileName);
    this.assignmentForm.uploadedFileSize.setValue(assignment.uploadedFileSize);
    this.assignmentForm.uploadedFileType.setValue(assignment.uploadedFileType);
    this.assignmentForm.isPublished.setValue(assignment.isPublished);
    this.assignmentForm.isRequired.setValue(assignment.isRequired);

  }


      /* 
    Initiate form
    */
    private _initForm(){
      this.form=this.formBuilder.group({
        lecture:this.lecture._id,
        description: ['', Validators.required],
   
        uploadedFile:[''],
        uploadedFileName:'',
        uploadedFileSize:'',
        uploadedFileType:'',
        encodedFileName:'',
        isRequired: ['', [Validators.required]],
        isPublished: ['', [Validators.required]],
       
      })
    
          }   
  inputChange(event:any){
    this.isInputUpdated.emit(true);
    this.inputUpdated = true;
    }
    
      
    /* 
    Scroll to form
    */

    ngAfterViewInit() {
      window.scrollTo(1900, 540);
    }


    /*
   Cancel any updates
   */ 
   cancelAssignment(){
      this.deleteUploadedFile();
      this.isAssignmentCanceled.emit(true);
    }

    /* 
    Reset Form With Default Values
    */
    resetFormWithDefaultValues() {
      this.isInputUpdated.emit(false);
      this.inputUpdated = false;
  
      this.assignmentEditForm.resetForm(this.currentAssignment);  
      this.deleteUploadedFile();
    }

    deleteUploadedFile(){
      this.fileInputVariable.nativeElement.value = "";
      this.uploader.destroy();
      this.uploader.clearQueue();
     }


/**
 * upload file
 * @param event 
 * @returns 
 */


     uploadAssigmentFile(event:any){
      if (this.uploader.queue.length>1) {
        this.uploader.queue[0].remove();
      }
      const file = event.target.files[0];
  
      this.uploader.onWhenAddingFileFailed = (file) => {
      this.error = "File type is not supported";
      this.deleteUploadedFile();
      }
  
      if (file) {
    
        let uploadedFileName =this.uploader.queue[0]?.file?.name;
        let uploadedFileSize = (this.uploader.queue[0]?.file?.size / 1024 / 1024).toFixed(2)+' MB';
        let uploadedFileType =this.uploader.queue[0]?.file?.type;
    
        this.form.patchValue({uploadedFile:file});
        this.form.patchValue({uploadedFileName:uploadedFileName});
        this.form.patchValue({uploadedFileSize:uploadedFileSize});
        this.form.patchValue({uploadedFileType:uploadedFileType});
       
        this.form.get('uploadedFile').updateValueAndValidity();
        const fileReader = new FileReader();
        fileReader.onload = () =>{
          this.uploadedFile = fileReader.result
        };
        fileReader.readAsDataURL(file);
    }
  }


     assignmentFormSubmit(){

      this.isUpdatedSuccess.emit(false);
      this.isSubmitted = true;
  
      if (this.form.invalid) return;
  
      const assignmentFormData = new FormData();
      Object.keys(this.assignmentForm).map((key)=>{
        assignmentFormData.append(key,this.assignmentForm[key].value);
       });
      
      this._updateOrCreateAssignment(assignmentFormData);
     }


     _updateOrCreateAssignment(assignmentFormData: FormData) {

      this._assignmentService.updateAssignment(
        assignmentFormData,
         this.currentAssignment?.id
       )
       .subscribe((event: HttpEvent<any>) => {
   
         switch (event.type) {
      
           case HttpEventType.Sent:
             break;
           case HttpEventType.ResponseHeader:
             break;
           case HttpEventType.UploadProgress:
             this.progress = Math.round(event.loaded / event.total * 100);
             this.loading = true;
   
             break;
           case HttpEventType.Response:
   
             this.currentAssignment = event.body;
             this.assignment = event.body;
             this.loading = false;
   
             this.isUpdatedSuccess.emit(true);
             this.isInputUpdated.emit(false);
             
             this.toastr.success(this.editMode?'Content updated successfuly!':'Content created successfuly!', 'Success!', {
               toastClass: 'toast ngx-toastr',
               closeButton: true
              });

         setTimeout(() => {
               this.progress = 0;
             }, 1500);
             this.deleteUploadedFile();
         }
       },  (err) => {
         this.toastr.error(this.editMode?'Content wasn\'t updated!':'Content wasn\'t created!', 'Error!', {
           toastClass: 'toast ngx-toastr',
           closeButton: true
         });
       this.setErrorMessage(this.editMode?'Update failed, something went wrong':'Create document failed, something went wrong');
       this.loading = false; 
       });
     }

     setErrorMessage(message: string, timeOut = 3000) {
      this.error = message;
    this.ngAfterViewInit();
      setTimeout(() => {
        this.error = null;
      }, timeOut);
    }
     get assignmentForm(){
      return this.form.controls;
    }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this.onDestroy.next();
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();

    }
  
}
