import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Input, Component, OnInit, ViewChild, EventEmitter, Output, ElementRef } from '@angular/core';
import {FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';

import { AssetService } from 'app/services/asset.service';
import { Subject } from 'rxjs';


import { CommonService } from 'app/services/common.service';
import { ToastrService } from 'ngx-toastr';
import { Lecture } from 'app/models/lecture';


@Component({
  selector: 'asset-external-form',
  templateUrl: './asset-external-form.component.html'
})
export class AssetExternalFormComponent implements OnInit {

  @ViewChild('fileInputVariable') fileInputVariable: ElementRef;
  @ViewChild('assetEditExternalForm') assetEditExternalForm: NgForm;

  public currentRow: any;
  public error:string;


  public uploadedFile:string| ArrayBuffer;
  isSubmitted = false;
  form:FormGroup;
  currentAssetId:string;

  progress: number = 0;

  public editMode = false;

  public inputUpdated = false;
  public loading:boolean = false;



  // Input Decorator
   @Input() lecture: Lecture;
   @Input() asset: any;
   @Output() isUpdatedSuccess = new EventEmitter<boolean>();
   @Output() isCancelNewExternal = new EventEmitter<boolean>();
   @Output() isInputUpdated = new EventEmitter<boolean>();

  // private
  private _unsubscribeAll: Subject<any>;


  /**
   * Constructor
   *
   */
  constructor(
    private _assetSerivce:AssetService,
    private toastr: ToastrService,
    private formBuilder:FormBuilder,
    private _commonService:CommonService,
    ) { 
      this._unsubscribeAll = new Subject();
    }
  /**
   * On Initial
   *
   */

    ngOnInit(): void {

      this.isUpdatedSuccess.emit(false);
      this.isCancelNewExternal.emit(false);
      this.isInputUpdated.emit(false);
    
      this._initForm();
     if(this.asset)
     {
      this.editMode = true;
      this.currentRow = this.asset;
      this.currentAssetId=this.asset.id;
      this._setFormValues(this.asset);
     }

    }
// ----- /End initiate

inputChange(event:any){
this.isInputUpdated.emit(true);
this.inputUpdated = true;
}

   /*
   Cancel any updates
   */ 
    cancelNewExternal(){
      this.isCancelNewExternal.emit(true);
    }

    /* 
    Scroll to form
    */

    ngAfterViewInit() {
      window.scrollTo(1900, 540);
    }

 
    /* 
    Reset Form With Default Values
    */
   resetFormWithDefaultValues() {
    this.isInputUpdated.emit(false);
    this.inputUpdated = false;
    this.assetEditExternalForm.resetForm(this.currentRow);

  }

      /* 
    Initiate form
    */
private _initForm(){
  this.form=this.formBuilder.group({
    lecture:this.lecture.id,
    description: ['', Validators.required],
    section: 'lecture_explenation',
    fileType:'external',
    uploadedFile:['', Validators.required],
    uploadedFileName:'',
    uploadedFileSize:'',
    uploadedFileType:'',
    encodedFileName:'',

    duration:'',
    hours:'',
    minutes:'',
    seconds:'',
    
    isPublished:true,
    isExternal:false,
  })

      }   

  /**
   * set value if this in update mode
   * 
   * @param asset 
   */    
   
   private _setFormValues(asset:any){
        
        this.assetExternalForm.description.setValue(asset.description);

        this.assetExternalForm.uploadedFile.setValue(asset.uploadedFile);
        this.assetExternalForm.uploadedFileName.setValue(asset.uploadedFileName);
        this.assetExternalForm.encodedFileName.setValue(asset.encodedFileName);
        this.assetExternalForm.uploadedFileSize.setValue(asset.uploadedFileSize);
        this.assetExternalForm.uploadedFileType.setValue(asset.uploadedFileType);

        this.assetExternalForm.duration.setValue(asset.duration);
       
        this.assetExternalForm.isExternal.setValue(asset.isExternal);
        this.assetExternalForm.isPublished.setValue(asset.isPublished);

      }


  /**
   * On Submit
   *
   */
   assetExternalFormSubmit() {
    this.isUpdatedSuccess.emit(false);
    this.isSubmitted = true;

    if (this.form.invalid) return;

    const assetExternalFormData = new FormData();
    Object.keys(this.assetExternalForm).map((key)=>{
      assetExternalFormData.append(key,this.assetExternalForm[key].value);
     });
    this._updateAsset(assetExternalFormData);
  }


  _updateAsset(assetExternalFormData: FormData) {
   this._assetSerivce.updateAsset(
      assetExternalFormData,
      this.currentAssetId
    )
    .subscribe((event: HttpEvent<any>) => {

      switch (event.type) {
   
        case HttpEventType.Sent:
          break;

        case HttpEventType.ResponseHeader:
          break;
  
        case HttpEventType.Response:

          this.currentRow = event.body;
          this.asset = event.body;
          this.loading = false;

          this.isUpdatedSuccess.emit(true);
          this.isInputUpdated.emit(false);
          
          this.toastr.success(this.editMode?'Content updated successfuly!':'Content created successfuly!', 'Success!', {
            toastClass: 'toast ngx-toastr',
            closeButton: true
          });
      setTimeout(() => {
            this.progress = 0;
          }, 1500);
      }
    },  (err) => {
      this.toastr.error(this.editMode?'Content wasn\'t updated!':'Content wasn\'t created!', 'Error!', {
        toastClass: 'toast ngx-toastr',
        closeButton: true
      });
    this.setErrorMessage(this.editMode?'Update failed, something went wrong':'Create audio failed, something went wrong');
    this.loading = false; 
    });
  }


  get assetExternalForm(){
    return this.form.controls;
  }
    /**
   * On destroy
   */
     ngOnDestroy(): void {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next();
      this._unsubscribeAll.complete();
    }

    setErrorMessage(message: string, timeOut = 3000) {
      this.error = message;
    this.ngAfterViewInit();
      setTimeout(() => {
        this.error = null;
      }, timeOut);
    }
}
